import axios from 'axios';
// 导入数据序列化
import qs from 'qs';
// 导入日期处理类库
import moment from 'moment';
import { MessageBox } from 'element-ui';
import router from '@/router';
import '@/utils/dateFormat';
import { IP } from './constantsFile';
import { NETWORK_URL, LOGO_CONFIG, LOGO } from '@/utils/ajaxConfigure';
// import { config } from 'vuedraggable';

/**
 * @description: 自定义默认值
 * @param {type}
 * @return:
 * @author: YZZ
 */
export const instance = axios.create({
  // 请求类型
  // method: 'post',
  // 设置前缀
  // baseURL: 'http://192.168.1.92:8080/amk',
  baseURL: IP.PUBLICIP,
  // 设置超时
  timeout: 90000
});
instance.interceptors.request.use(
  config => {
    if (window.localStorage.getItem('bearer')) {
      config.headers.bearer = window.localStorage.getItem('bearer');
    }
    if (window.localStorage.getItem('token') || getUrlKey('token', window.location.href)) {
      if (window.localStorage.getItem('token')) {
        const token = window.localStorage.getItem('token').replace(/"/g, '');
        config.headers.token = token;
        if (window.localStorage.getItem('bearer')) {
          config.headers.bearer = window.localStorage.getItem('bearer');
        }
      } else {
        var tokenStr = getUrlKey('token', window.location.href);
        const token = tokenStr.replace(/"/g, '');
        config.headers.token = token;
      }
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

// 响应拦截，如果token失效或者未登录，跳转登录页面
instance.interceptors.response.use(
  response => {
    if (response.data.code === '10010') {
      if (
        router.currentRoute.path !== '/login' &&
        router.currentRoute.path !== '/register'
      ) {
        if (window.localStorage.getItem('token') || getUrlKey('token', window.location.href)) {
          window.localStorage.clear();
          if (router.currentRoute.path !== '/login') {
            MessageBox.alert(response.data.message, '重新登录', {
              confirmButtonText: '确定',
              type: 'warning',
              center: false,
              callback: action => {
                // console.log(555);
                if (router.currentRoute.path.indexOf('workstation') === -1) {
                  var Logo = '';
                  if (LOGO_CONFIG[localStorage.getItem('companyId')]) {
                    Logo = LOGO_CONFIG[localStorage.getItem('companyId')].logo;
                  } else {
                    Logo = LOGO;
                  }
                  if (Logo !== 'null') {
                    router.push({
                      path: '/login'
                    });
                  } else {
                    router.push({
                      path: '/loginPage'
                    });
                  }
                } else {
                  router.push({
                    path: '/loginWorkStation'
                  });
                }
              }
            });
          }
        } else if (window.localStorage.getItem('token') === null) {
          if (router.currentRoute.path !== '/login') {
            if (router.currentRoute.path.indexOf('workstation') === -1) {
              // console.log(666);
              var Logo = '';
              if (LOGO_CONFIG[localStorage.getItem('companyId')]) {
                Logo = LOGO_CONFIG[localStorage.getItem('companyId')].logo;
              } else {
                Logo = LOGO;
              }
              if (Logo !== 'null') {
                router.push({
                  path: '/login'
                });
              } else {
                router.push({
                  path: '/loginPage'
                });
              }
            } else {
              router.push({
                path: '/loginWorkStation'
              });
            }
          }
        }
      }
    }
    return response;
  },
  error => {
    return Promise.reject(error);
  }
);

