/**
 * @description: get方法，对应get请求
 * @param {String} url 请求的url地址
 * @param {Object} params 请求时携带的参数
 * @return:
 * @author: YZZ
 */
export function get(url, params) {
  // var fullUrl = instance.defaults.baseURL + '/' + url;
  var fullUrl = instance.defaults.baseURL + url;
  return new Promise((resolve, reject) => {
    instance2
      .get(fullUrl, {
        params: params
      })
      .then(res => {
        resolve(res.data);
      })
      .catch(err => {
        reject(err.data);
      });
  });
}

