/**
 * @description: post方法，对应post请求
 * @param {String} url 请求的url地址
 * @param {Object} params 请求时携带的参数
 * @return:
 * @author: YZZ
 */
export function post(url, params) {
  return new Promise((resolve, reject) => {
    instance
      .post(
        url,
        qs.stringify(params, {
          indices: false,
          serializeDate: data => {
            return moment(data).format('YYYY-MM-DD HH:mm:ss');
          }
        })
      )
      .then(res => {
        resolve(res.data);
      })
      .catch(err => {
        reject(err.data);
      });
  });
}

