/**
 * @description: post方法，对应post请求，用于aplication/json方式请求
 * @param url 请求的url地址
 * @param param form-json数据
 * @param jsonData json或arr对象数据
 * @return:无
 * @author: ZJM
 */
export function postAplicationJSON(url, param, jsonData, config = {}) {
  const paramStr = qs.stringify(param, {
    indices: false,
    serializeDate: data => {
      return moment(data).format('YYYY-MM-DD HH:mm:ss');
    }
  });
  return new Promise((resolve, reject) => {
    instance
      .post(url + paramStr, jsonData, config)
      .then(res => {
        resolve(res.data);
      })
      .catch(err => {
        reject(err.data);
      });
  });
}

export function postAplicationJSONNetWork(url, param, jsonData, config = {}) {
  const paramStr = qs.stringify(param, {
    indices: false,
    serializeDate: data => {
      return moment(data).format('YYYY-MM-DD HH:mm:ss');
    }
  });
  return new Promise((resolve, reject) => {
    instance
      .post(NETWORK_URL + url + paramStr, jsonData, config)
      .then(res => {
        if (res.data.code === 0) {
          resolve(res.data);
        } else {
          reject(res.data);
        }
      })
      .catch(err => {
        reject(err.data);
      });
  });
}

