<template>
  <div class="tags">
    <ul>
      <li
        class="tags-li fixed"
        :class="{ active: isActive(routeUrl) }"
        width="'40px'"
      >
        <router-link
          :to="routeUrl"
          class="tags-li-title"
          :class="[
            isActive(routeUrl) ? 'masterControl_check' : 'masterControl_unCheck'
          ]"
        ></router-link>
      </li>
      <li
        class="tags-li fixed fixed-right"
        :class="{ active: isActive('/setCommonFunctions') }"
        width="'40px'"
      >
        <router-link
          :to="'/setCommonFunctions'"
          class="tags-li-title"
          :class="[
            isActive('/setCommonFunctions')
              ? 'navigation_check'
              : 'navigation_unCheck'
          ]"
        ></router-link>
      </li>
      <li
        class="tags-li"
        v-for="(item, index) in tagsList"
        :class="{ active: isActive(item.path) }"
        :key="index"
        width="140px"
      >
        <router-link :to="item.path" class="tags-li-title">{{
          item.title
        }}</router-link>
        <span class="tags-li-icon" @click="closeTags(index)">
          <i class="el-icon-close"></i>
        </span>
      </li>
    </ul>
    <!-- <div class="tags-close-box">
      <el-dropdown @command="handleTags" style="position:absolute;">
        <el-button size="mini" type="primary" style="position:absolute;left:-40px;">
          标签选项
          <i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu size="small" slot="dropdown">
          <el-dropdown-item command="other">关闭其他</el-dropdown-item>
          <el-dropdown-item command="all">关闭所有</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div> -->
  </div>
</template>

<script>
import { ROUTER_CONFIG } from '@/utils/ajaxConfigure';
import bus from '../../plugins/bus';
export default {
  data() {
    return {
      tagsList: [],
      indexarr: [],
      routeUrl: '/MainControlRoom'
    };
  },
  methods: {
    // li 是否被点击
    isActive(path) {
      // if (this.$route.fullPath){}
      return path === this.$route.fullPath;
    },
    // 是不是第一个主页class
    isfirst(path) {
      if (path === '/index') {
        return true;
      }
    },
    // 第一个是否显示× 和换成图片
    showfault(path) {
      return path === '/index';
    },
    // 关闭单个标签
    closeTags(index) {
      console.log('101', index);

      const delItem = this.tagsList.splice(index, 1)[0];
      console.log('102', delItem);
      const item = this.tagsList[index]
        ? this.tagsList[index]
        : this.tagsList[index - 1];
      console.log('003', item);
      if (item) {
        delItem.path === this.$route.fullPath && this.$router.push(item.path);
      } else {
        if (ROUTER_CONFIG[localStorage.getItem('companyId')]) {
          const path = ROUTER_CONFIG[localStorage.getItem('companyId')].url;
          this.$router.push(path);
        } else {
          this.$router.push('/');
        }
      }
    },
    // 关闭全部标签
    closeAll() {
      this.tagsList = [];
      this.$router.push('/');
    },
    // 关闭其他标签
    closeOther() {
      const curItem = this.tagsList.filter(item => {
        return item.path === this.$route.fullPath;
      });
      this.tagsList = curItem;
    },
    // 设置标签
    setTags(route) {
      let isExist = this.tagsList.some(item => {
        return item.path === route.fullPath;
      });
      if (
        route.fullPath === this.routeUrl ||
        route.path === '/setCommonFunctions'
      ) {
        isExist = true;
      }
      if (route.path === '/equipment/deviceStatusInfo') {
        isExist = true;
      }
      if (!isExist) {
        if (this.tagsList.length >= 10) {
          this.tagsList.shift();
        }
        if (route.path === '/index') {
          this.tagsList.push({
            title: '',
            path: route.fullPath,
            name: route.matched[1].components.default.name
          });
        } else {
          this.tagsList.push({
            title: route.meta.title.replace('产量', this.$store.state.pageInfo.pageConfigInfo.yieldKey),
            path: route.fullPath,
            name: route.matched[1].components.default.name
          });
        }
      }
      if (route.path === '/home') {
        this.$router.push('/index');
        this.tagsList = [];
      }
      bus.$emit('tags', this.tagsList);
    },
    handleTags(command) {
      command === 'other' ? this.closeOther() : this.closeAll();
    }
  },
  computed: {
    showTags() {
      return this.tagsList.length > 0;
    }
  },
  watch: {
    $route(newValue, oldValue) {
      this.setTags(newValue);
    }
  },
  created() {
    if (ROUTER_CONFIG[localStorage.getItem('companyId')]) {
      this.routeUrl = ROUTER_CONFIG[localStorage.getItem('companyId')].url;
    }

    this.setTags(this.$route);
    // 监听关闭当前页面的标签页
    bus.$on('close_current_tags', () => {
      for (let i = 0, len = this.tagsList.length; i < len; i++) {
        const item = this.tagsList[i];
        if (item.path === this.$route.fullPath) {
          if (i < len - 1) {
            this.$router.push(this.tagsList[i + 1].path);
          } else if (i > 0) {
            this.$router.push(this.tagsList[i - 1].path);
          } else {
            this.$router.push('/');
          }
          this.tagsList.splice(i, 1);
          break;
        }
      }
    });
  }
};
</script>

<style lang="less" scoped>
/* 外框大小 */
@outerFrameHeight: 40px;
/* 里面按钮大小 */
@insideHight: 30px;
.tags {
  position: relative;
  height: 40px;
  border-top: 1px solid #b5b1b1;
  border-bottom: 1px solid #b5b1b1;
}

.tags ul {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  margin: 0;
  float: left;
  padding: 0;
}

.tags-li {
  display: flex;
  align-items: center;
  float: left;
  font-size: 14px;
  font-weight: 600;
  overflow: hidden;
  cursor: pointer;
  height: @insideHight;
  background: #fff;
  padding: 0 15px;
  color: #666;
  border: 1px solid #dbdbdb;
  -webkit-transition: all 0.3s ease-in;
  -moz-transition: all 0.3s ease-in;
  transition: all 0.3s ease-in;
  margin-top: 4px;
  margin-left: 8px;
  a {
    height: @insideHight;
    line-height: @insideHight;
  }
}

.tags-li:not(.active):hover {
  span {
    // visibility: visible;
    color: #157be0;
  }
}
.tags-li:not(.active) {
  background: #fff;
  color: #333;
  // span{
  //   visibility: hidden;
  // }
}

.tags-li.active {
  background: #157be0;
  color: #fff;
  border-color: #157be0;
}

.tags-li-title {
  float: left;
  // max-width: 80px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-right: 5px;
  color: #969494;
  text-decoration: none;
}

.tags-li.active .tags-li-title,
.tags-li.active .el-icon-close {
  color: #fff;
}

.tags-close-box {
  position: absolute;
  right: 0;
  top: 0;
  box-sizing: border-box;
  padding-top: 1px;
  text-align: center;
  width: 110px;
  height: 50px;
  background: #0871dc;
  box-shadow: -3px 0 15px 3px rgba(0, 0, 0, 0.1);
  z-index: 10;
}

.el-icon-close {
  color: #333;
  font-size: 16px;
}

.activefault {
  display: none;
}

.activefirst {
  background-image: url(../../assets/img/TabBar/common_icon_wodezhukongshi_blue.png);
  background-repeat: no-repeat;
  background-position: left;
}
.fixed {
  padding: 0;
  border: 0;
  .tags-li-title {
    margin-right: 0;
  }
}

.fixed-right {
  margin-left: 8px;
}
.masterControl_check {
  background-image: url('~@/assets/img/tags/masterControl_check.png');
  background-repeat: round;
  width: @insideHight;
  height: @insideHight;
}
.masterControl_unCheck {
  background-image: url('~@/assets/img/tags/masterControl_unCheck.png');
  background-repeat: round;
  width: @insideHight;
  height: @insideHight;
}
.navigation {
  margin-left: 8px;
}
.navigation_check {
  background-image: url('~@/assets/img/tags/navigation_check.png');
  background-repeat: round;
  width: @insideHight;
  height: @insideHight;
}
.navigation_unCheck {
  background-image: url('~@/assets/img/tags/navigation_unCheck.png');
  background-repeat: round;
  width: @insideHight;
  height: @insideHight;
}
</style>
