<template>
  <div class="header border-bottom-common" style="position: relative">
    <div class="titleDiv" v-if="showTitle">
      <span class="head-title-span">{{ strFunc(titleName) }}</span>
    </div>

    <div :style="getStylesDiv()">
      <div class="margin-left-20 primary-search" style="display: flex">
        <!-- <div  class="more">
          <span class="head-title-span">{{titleName}}</span>
        </div> -->
        <div v-if="showRefresh" class="more">
          <a @click="refreshData">
            <i class="el-icon-refresh icon-jiahao" style="color: #147be0; font-weight: 600"></i>
            <span>刷新</span>
          </a>
        </div>
        <!-- <div class="more">
          <a href="javascript:more();">
            <i
              class="el-icon-view icon-jiahao"
              style="color:#147BE0;fontWeight:600;"
            ></i>
            <span>浏览</span>
          </a>
        </div> -->
        <!-- 功能按钮 -->
        <div class="more" :style="getStyles(item)" v-for="item in pageInfo" :key="item.id">
          <a v-if="!item.hidden" @click="buttonClickEvent1(item.action, item.cssType)">
            <i :class="`${item.icon || 'el-icon-document'} icon-jiahao`" style="color: #147be0; font-weight: 600"></i>
            <span> {{ item.buttonName }}</span>
          </a>
        </div>

        <div class="more" v-if="synchronousOption && synchronousOption.includes('1') && isAMICO">
          <a href="javascript:more();" @click="synchronous('1')">
            <i class="el-icon-view icon-jiahao" style="color: #147be0; font-weight: 600"></i>
            <span>同步工单</span>
          </a>
        </div>
        <div class="more" v-if="synchronousOption && synchronousOption.includes('1') && isAMICO">
          <a href="javascript:more();" @click="synchronous('1')">
            <i class="el-icon-view icon-jiahao" style="color: #147be0; font-weight: 600"></i>
            <span>同步工单</span>
          </a>
        </div>
        <div class="more" v-if="synchronousOption && synchronousOption.includes('2')">
          <a href="javascript:more();" @click="synchronous('2')">
            <i class="el-icon-view icon-jiahao" style="color: #147be0; font-weight: 600"></i>
            <span>同步库存</span>
          </a>
        </div>
        <div class="more" v-if="synchronousOption && synchronousOption.includes('3') && isAMICO">
          <a href="javascript:more();" @click="synchronous('3')">
            <i class="el-icon-view icon-jiahao" style="color: #147be0; font-weight: 600"></i>
            <span>同步报工信息</span>
          </a>
        </div>
        <div class="more" v-if="synchronousOption && synchronousOption.includes('4') && isAMICO">
          <a href="javascript:more();" @click="synchronous('4')">
            <i class="el-icon-view icon-jiahao" style="color: #147be0; font-weight: 600"></i>
            <span>同步员工组</span>
          </a>
        </div>
        <slot class="custom-button" name="insertButton"></slot>
        <!-- <span  ></span> -->
        <slot style="display: inline-block" name="inputPlace"></slot>
      </div>
      <slot name="head-operation"></slot>
    </div>
    <div>
      <span style="position: absolute; right: 20px; top: 3px">
        <el-button class="custom-button" v-if="showBack" type="primary" @click="buttonClickEvent1('exit')">
          返回
        </el-button>

        <!-- 美康领料 -->
        <div v-if="showMkButton" class="more morex">
          <a @click="buttonClickEvent1('pick_mk')">
            <i class="el-icon-s-grid icon-jiahao" style="color: #147be0; font-weight: 600; margin-top:0;"></i>
            <span>领料单管理</span>
          </a>
        </div>
        <!-- 美康退料 -->
        <div v-if="showMkButton" class="more morex">
          <a @click="buttonClickEvent1('return_mk')">
            <i class="el-icon-s-grid icon-jiahao" style="color: #147be0; font-weight: 600; margin-top:0;"></i>
            <span>退料单管理</span>
          </a>
        </div>

      </span>
    </div>

    <div v-if="status == '10002' ||
      status == '10020' ||
      status == '10022' ||
      status == '10126' ||
      status == '10127' ||
      status == '10214'
      " style="
        position: absolute;
        width: 100px;
        height: 80px;
        right: 40px;
        top: 50px;
        z-index: 999;
      ">
      <i class="icon_pass"></i>
    </div>
    <div v-if="showReWrite">
      <div style="
          position: absolute;
          width: 100px;
          height: 80px;
          right: 40px;
          top: 100px;
          z-index: 999;
        ">
        <i class="icon_huixie"></i>
      </div>
    </div>

    <!-- <complexQury></complexQury> -->
  </div>
</template>

<script>
import { getUrlParams } from '@/utils/common';
import { showErrorMessage } from '@/utils/message';
// eslint-disable-next-line camelcase
import { IS_Mk } from '@/utils/ajaxConfigure';
// import { ENVIRONMENT } from '@/utils/ajaxConfigure';
import {
  syncAmicoInvInfoAPI,
  syncAmicoTaskInfoAPI,
  syncAmicoTaskOperInfotAPI,
  synchroAmicoErpTeamInfoAPI
} from '@/api/master';
export default {
  name: 'headerButtons',
  props: [
    'showBack',
    'toRight',
    'titleName',
    'showTitle',
    'showRefresh',
    'cssType',
    'pageTitle',
    'status',
    'showReWrite',
    'synchronousOption',
    'showType'
  ],

  data() {
    return {
      info: {},
      buttonList: [],
      showBtn: [],
      showMkButton: false
    };
  },
  watch: {},
  computed: {
    pageInfo() {
      var that = this;
      const id = getUrlParams().id;
      var json = { ...this.$store.state.pageInfo[id] };
      var arr = json.buttonList;
      if (that.showBtn.length > 0) {
        for (var i = 0; i < arr.length; i++) {
          if (arr[i].cssType === Number(that.cssType)) {
            var flag = 0;
            for (var j = 0; j < that.showBtn.length; j++) {
              if (arr[i].type === that.showBtn[j]) {
                flag += 1;
                arr[i].hidden = true;
              }
            }
            if (flag === 0) {
              arr[i].hidden = false;
            }
          }
        }
      }
      return arr;
    },
    isAMICO() {
      // return ENVIRONMENT === 'AMICO';
      return true;
    }
  },
  created() {
    var num = getUrlParams().id;
    if (num === '35' || num === '36') {
      // eslint-disable-next-line camelcase
      if (IS_Mk) {
        this.showMkButton = true;
      } else {
        this.showMkButton = false;
      }
    } else {
      this.showMkButton = false;
    }
  },
  methods: {
    // 刷新页面
    refresh() {
      location.reload();
    },
    refreshData() {
      this.$emit('buttonClickEvent', 'refresh', 1);
    },
    buttonClickEvent1(type, cssType) {
      if (type === 'goBack') {
        this.$router.go(-1);
        return;
      }
      console.log(type, cssType);
      this.$emit('buttonClickEvent', type, cssType);
    },
    async synchronous(type) {
      const loading = this.$loading({
        body: true,
        lock: true,
        text: '同步数据中...'
      });

      try {
        if (type === '1') {
          const res = await syncAmicoTaskInfoAPI();
          console.log(res);
        } else if (type === '2') {
          const res = await syncAmicoInvInfoAPI();
          console.log(res);
        } else if (type === '3') {
          const res = await syncAmicoTaskOperInfotAPI();
          console.log(res);
        } else if (type === '4') {
          const res = await synchroAmicoErpTeamInfoAPI();
          console.log(res);
        }

        loading.close();
      } catch (e) {
        loading.close();
        showErrorMessage(e);
      }
    },
    getStyles(item) {
      if (item.cssType === Number(this.cssType)) {
        if (this.showType === undefined) {
          return {};
        }
        const showType = this.showType.split(',');
        for (const x of showType) {
          if (item.type === x) {
            return {};
          }
        }
        // [...this.showType].map(va => {
        //   if (item.type === va) {
        //     return {};
        //   }
        // });
      }
      return { display: 'none' };
    },
    getStylesDiv() {
      if (this.toRight) {
        return { float: 'right' };
      }
    },
    strFunc(param) {
      if (param.length > 25) {
        return param.substring(0, 25) + '...';
      } else {
        return param;
      }
    }
  }
};
</script>

<style lang="less" scoped>
.header {
  background-color: #eff5ff;
  width: 100%;
  min-height: 40px;
  line-height: 36px;
  /*   display: flex; */
  align-items: center;
  justify-content: space-between;
}

.border-bottom-common {
  border-bottom: 1px solid #dedddd;
}

.custom-button {
  margin: 0 5px;
  display: flex;
}

.header-left-border {
  display: flex;
  align-items: center;
  margin-left: 12px;
  border-left: 4px solid #0871dc;
  height: 20px;
}

.head-title-span {
  border-left: 3px solid #0871dc;
  font-weight: bold;
  padding-left: 15px;
  color: #333;
}

.head-search {
  display: flex;
  align-items: center;
  margin-left: 14px;

  span {
    color: #999999;
  }
}

.margin-left-20 {
  margin-left: 0px;
}

.width-150 {
  width: 150px;
}

.width-100 {
  width: 100px;
}

.gap-10 {
  margin-right: 10px;
}

.primary-search /deep/ .el-input__inner {
  line-height: 31px;
  height: 31px;
}

@media screen and (max-width: 1440px) {
  .width-150 {
    width: 100px;
  }

  .width-100 {
    width: 80px;
  }

  .gap-10 {
    margin-right: 5px;
  }
}

@media screen and (max-width: 1280px) {
  .value-input {
    width: 90px;
  }
}

.more {
  display: block;
  height: 36px;
  /* margin: 0 auto; */
  text-align: center;
  color: #999;
}

.more {
  display: block;
  height: 36px;
  /* margin: 0 auto; */
  text-align: center;
  color: #999;
  cursor: pointer;
}

.more a {
  display: inline-block;
  height: 36px;
  line-height: 36px;
  font-size: 15px;
  padding: 0 10px;
  color: #666;
  border-radius: 4px;
  text-decoration: none;
}

.more :hover {
  background-color: rgb(181, 210, 244);
}

.morex {
  display: inline-block !important;
}

.icon-jiahao {
  display: inline-block;
  // float: left;
  margin-right: 5px;
  line-height: 36px;
  font-size: 18px;
  vertical-align: middle;
}

.titleDiv {
  float: left;
  padding-left: 15px;
}
</style>
