// let Validation = {};
exports.install = function(Vue, options) {
  /**
   * @description: 必填验证
   * @param {Array} item 传入参数
   *  item参数说明：
   *    required true 必填项
   *    name 必填项名称
   *    maxLength 字符串最大长度
   *    min、max 必须同时给 min < max 类型number
   *    type 电话号码 phone
   *         身份证 IDCard
   *
   * @return:
   * @author: YZZ
   */
  Vue.prototype.filter_rules = function(item) {
    const rules = [];
    if (item.required) {
      rules.push({ required: true, message: item.name + '不能为空!', trigger: 'blur' });
    }
    if (item.maxLength) {
      rules.push({ min: 1, max: item.maxLength, message: '最多输入' + item.maxLength + '个字符！', trigger: 'blur' });
    }
    if (item.min && item.max) {
      rules.push({ min: item.min, max: item.max, message: '字符长度在' + item.min + '至' + item.max + '之间！', trigger: 'blur' });
    }
    if (item.type) {
      const type = item.type;
      switch (type) {
        case 'phone':
          // 手机或座机号码验证
          rules.push({ pattern: /(^(\d{3,4}-)?\d{7,8})$|(^1[34578]\d{9})$/, message: '请输入正确电话号码', trigger: 'blur' });
          break;
        case 'IDCard':
          // 身份证验证
          rules.push({ pattern: /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/, message: '请输入正确身份证信息', trigger: 'blur' });
          break;
        default:
          rules.push({});
          break;
      }
    }
    return rules;
  };
};
