import { getPrintGRFAPI, postPrintAPI } from '@/api/base';
import { iterator } from '@/utils/common';
import { showErrorMessage, showSuccessMessage } from '@/utils/message';
import { Loading } from 'element-ui';
/**
 * 搜索Model的静态
 */

const menuList = {
  namespaced: true, // 命名空间
  // 初始化相关页面数据 否则第一个请求到数据不渲染
  state: {
    visible: false,
    id: ''
  },
  mutations: {
    changeVisible(state, payload) {
      state.visible = payload;
    }
  },
  actions: {
    /**
     * 获取菜单信息
     * @author zhf
     */
    async goPrint({ commit, state }, payload) {
      if (!payload) {
        commit('changeVisible', false);
        return;
      }
      const loadingInstance = Loading.service({ fullscreen: true });
      const { num = 1 } = payload;
      try {
        // 获取打印文件地址和name
        const { code, message, type, desc } = await getPrintGRFAPI(payload);
        loadingInstance.close();
        if (code === '0000') {
          iterator(postPrintAPI, {
            method: 'printreport',
            ReportType: 'gridreport',
            ReportName: type,
            ReportVersion: '1',
            ReportUrl: desc,
            Copies: '1',
            Duplex: '0',
            vue: '1', // 需要设置为 1, 要不 局域网打印等 含有\\的无法识别 , vue 提交的数据需要把\\替换为\
            PrinterName: '', // 这个无法转义
            Preview: payload.preview || 0,
            token: 'aa',
            taskId: '1234567'
          }, Number(num));

          showSuccessMessage('正在打印...');
          commit('changeVisible', false);
        } else {
          commit('changeVisible', false);
          showErrorMessage(message);
        }
      } catch (error) {
        commit('changeVisible', false);
        loadingInstance.close();
        showErrorMessage(error);
      }
    },
    /**
     * 获取菜单信息
     * @author zhf
     */
    async openPrint({ commit, state }, payload) {
      commit('changeVisible', true);
    }
  },
  getters: {}
};

export default menuList;
