// 网络请求封装
export function request(config) {
  // 用promise链式调用
  // 创建axios实例
  const instance = axios.create({
    // 请求类型
    // method: '',
    // 设置前缀
    // baseURL: 'http://192.168.1.15:8080/idevcloudweb'
    baseURL: 'http://127.0.0.1/easy/'
    // 设置超时
    // timeout: 1000
  });

  // 发送真正的网络请求（本身就返回promise）
  return instance(config);
};
