import { postAplicationJSON } from '@/utils/request';

// 查询角色列表
export const roleGetRoleListAction = (param, jsonData) => postAplicationJSON('role/getRoleList.action?', param, jsonData);
// 新增角色
export const roleInsertAction = (param, jsonData) => postAplicationJSON('role/insert.action?', param, jsonData);
// 修改角色
export const roleUpdateByIdAction = (param, jsonData) => postAplicationJSON('role/updateById.action?', param, jsonData);
// 批量删除角色
export const roleDeleteByIdsAction = (param, jsonData) => postAplicationJSON('role/deleteByIds.action?', param, jsonData);

// 获取权限列表
export const powerGetListPowerAction = (param, jsonData) => postAplicationJSON('power/getListPower.action?', param, jsonData);
// 根据角色id查询关联的权限
export const rolePowerRelationshipGetListPowerByRoleIdAction = (param, jsonData) => postAplicationJSON('rolePowerRelationship/getListPowerByRoleId.action?', param, jsonData);
// 角色关联权限,并且会删除之前关联的权限
export const rolePowerRelationshipUpdateRoleRelationshipPowerAction = (param, jsonData) => postAplicationJSON('rolePowerRelationship/updateRoleRelationshipPower.action?', param, jsonData);
// 查询所有用户
export const userGetAllAction = (param, jsonData) => postAplicationJSON('user/getAll.action?', param, jsonData);
// 根据当前登录的人查询权限菜单
export const userGetListUserPowerAction = (param, jsonData) => postAplicationJSON('user/getListUserPower.action?', param, jsonData);
// 角色关联用户,删除之前关联的角色
export const userRoleRelationshipUpdateRoleRelationshipUserAction = (param, jsonData) => postAplicationJSON('userRoleRelationship/updateRoleRelationshipUser.action?', param, jsonData);
export const updateRoleRelationshipUserBody = (param, jsonData) => postAplicationJSON('userRoleRelationship/updateRoleRelationshipUserBody.action?', param, jsonData);
// 角色关联页面按钮权限
export const rolePowerRelationshipUpdateRoleButtonRelationshipAction = (param, jsonData) => postAplicationJSON('rolePowerRelationship/updateRoleButtonRelationship.action?', param, jsonData);
