import Vue from 'vue';
/**
 * @description: 确认提示框组件（如果没有传msg，title，type则用默认值）
 * @param {String} msg 提示内容，默认值：确认要删除数据？
 * @param {String} title 提示标题，默认值：提示
 * @param {String} type 提示类型，默认值：warning
 * @param {Array} settings 传入参数
 * @return:
 * @author: YZZ
 */
Vue.prototype.showConfirm = function alert(msg = '确认要删除数据？', title = '提示', type = 'warning', settings = {}) {
  Object.assign(settings, { // 合并对象
    confirmButtonText: '确定',
    cancelButtonText: '取消',
    type: type,
    dangerouslyUseHTMLString: true // 允许确认框内容为html格式
  });
  return this.$confirm(msg, title, settings);
  // return this.$confirm(`<p style="font-weight:bold;">${msg}</p>`, title, settings);
};

