<!--
 * @Descripttion: vxe-model组件封装
 * @version:
 * @Author: YZZ
 * @Date: 2021-11-03 09:34:32
 * @LastEditors: ZJM
 * @LastEditTime: 2022-01-04 11:49:31
-->
<template>
  <div class="model-container">
    <vxe-modal
      v-model="visible"
      :id="modalID"
      :ref="modalID"
      :width="width"
      :height="height"
      :min-width="minWidth"
      :min-height="minHeigt"
      :show-zoom="showZoom"
      :resize="resize"
      :remember="remember"
      :storage="storage"
      :transfer="transfer"
      :showHeader="showHeader"
      :showFooter="showFooter"
      :mask="mask"
      :mask-closable="maskClosable"
      :margin-size="marginSize"
      :lock-view="lockView"
      :destroy-on-close="destroyOnClose"
      @show="dialogOpened"
      @close="closeModal"
    >
      <!-- 标题部分 -->
      <template #title>
        <slot name="title">
          <span class="model-title">{{ modalName }}</span>
        </slot>
      </template>
      <!-- 内容部分 -->
      <template #default>
        <slot name="default"></slot>
      </template>
      <!-- 底部按钮部分 -->
      <template #footer>
        <slot name="footer" class="button-row"></slot>
      </template>
    </vxe-modal>
  </div>
</template>

<script>
export default {
  name: 'modalVxeModal',
  data() {
    return {
      visible: this.show
    };
  },
  props: {
    // ID
    modalID: {
      type: String
    },
    // 标题
    modalName: {
      type: String,
      required: true
    },
    // 是否显示
    show: {
      type: Boolean,
      default: false
    },
    // 窗口的宽度
    width: {
      type: [Number, String],
      default: '45%'
    },
    // 窗口的高度
    height: {
      type: [Number, String],
      default: '40%'
    },
    // 窗口的最小宽度
    minWidth: {
      type: [Number, String],
      default: 0
    },
    // 窗口的最小高度
    minHeigt: {
      type: [Number, String],
      default: 0
    },
    // 标题是否标显示最大化与还原按钮
    showZoom: {
      type: Boolean,
      default: false
    },
    // 是否允许窗口边缘拖动调整窗口大小
    resize: {
      type: Boolean,
      default: false
    },
    // 记忆功能，会记住最后操作状态，再次打开窗口时还原窗口状态
    remember: {
      type: Boolean,
      default: false
    },
    // 是否启用 localStorage 本地保存，会将窗口拖动的状态保存到本地（需要有 id）
    storage: {
      type: Boolean,
      default: false
    },
    // 是否将弹框容器插入于 body 内
    transfer: {
      type: Boolean,
      default: false
    },
    // 是否显示头部
    showHeader: {
      type: Boolean,
      default: true
    },
    // 是否显示底部
    showFooter: {
      type: Boolean,
      default: true
    },
    // 是否显示遮罩层
    mask: {
      type: Boolean,
      default: false
    },
    // 是否允许点击遮罩层关闭窗口
    maskClosable: {
      type: Boolean,
      default: false
    },
    // 只对 resize 启用后有效，用于设置可拖动界限范围，如果为负数则允许拖动超出屏幕边界
    marginSize: {
      type: Number,
      default: -600
    },
    // 是否锁住页面，不允许窗口之外的任何操作
    lockView: {
      type: Boolean,
      default: false
    },
    // 在窗口关闭时销毁内容
    destroyOnClose: {
      type: Boolean,
      default: true
    }
  },
  methods: {
    /**
     * @description: 模态框关闭
     * @param {type}
     * @return:
     * @author: YZZ
     */
    closeModal() {
      // this.$emit('update:show', false);
      this.$emit('close');
    },

    dialogOpened() {
      // this.closeModal();
      this.$emit('opened');
    }
  },
  watch: {
    show() {
      this.visible = this.show;
    }
  },
  created() {
    // console.log('show', this.show);
    // console.log('id', this.modalID);
  }
};
</script>

<style lang="less" scoped>
.model-container {

}
::v-deep.vxe-modal--wrapper .vxe-modal--header {
  background-color: #fff;
}

.model-title {
  border-left: 4px solid #0871DC;
  padding-left: 8px;
  font-weight: bold;
}

// 设置中间内容样式
::v-deep.vxe-modal--wrapper .vxe-modal--body {
  padding-bottom: 0;
}

// 设置底部样式
::v-deep.vxe-modal--wrapper .vxe-modal--footer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-top: 0;
  // 设置底部button之间的距离，除最后个
  .el-button:not(:last-child) {
    margin-right: 30px;
  }
}
</style>
