<!--
 * @Descripttion: 导航菜单组件
 * @version:
 * @Author: YZZ
 * @Date: 2020-04-07 11:48:57
 * @LastEditors: ZJM
 * @LastEditTime: 2022-10-28 16:31:39
 -->
<template>
  <fragment>
    <template v-for="navMenu in navMenus">
      <!-- 子嵌套子菜单 -->
      <el-submenu
        v-if="navMenu.children != undefined && navMenu.entity"
        :index="navMenu.entity.menuName"
        :key="navMenu.entity.id"
      >
        <template slot="title">
          <img
            class="pic-text-spacing"
            :src="
              require('../../../assets/img/SideNavBar' + navMenu.entity.icon)
            "
            alt
          />
          <span slot="title" class="position">{{
            navMenu.entity.menuName
          }}</span>
        </template>
        <NavMenu :navMenus="navMenu.children"></NavMenu>
      </el-submenu>
      <!-- 直接链接的形式（没有子菜单的） -->
      <el-menu-item
        v-else
        :index="navMenu.entity.menuName"
        @click="handleRouter(navMenu)"
        :key="navMenu.entity.id"
      >
        <img
          class="pic-text-spacing"
          :src="require('../../../assets/img/SideNavBar' + navMenu.entity.icon)"
          alt
        />
        <span slot="title" class="position">{{ navMenu.entity.menuName }}</span>
      </el-menu-item>
    </template>
  </fragment>
</template>

<script>
import { DNC_URL, MES_URL } from '@/utils/ajaxConfigure';
export default {
  // 必须要写
  name: 'NavMenu',
  // 从父组件里接收到的参数
  props: ['navMenus'],
  data() {
    return {};
  },
  methods: {
    /**
     * @description: 点击直接链接形式的菜单后，实现路由跳转
     * @param {Object} navMenu
     * @return:
     * @author: YZZ
     */
    handleRouter(navMenu) {
      console.log(navMenu, MES_URL);
      // 把后端传过来的路径放入路由，实现路由跳转
      if (navMenu.entity.menuName === 'DNC系统') {
        const token = localStorage.getItem('token') || '';
        // const url = `${DNC_URL}?key=${token}`;
        console.log(token);
        console.log(DNC_URL);
        window.open(DNC_URL, token);
      } else if (navMenu.entity.menuName === 'MES系统') {
        // eslint-disable-next-line no-redeclare
        var token = '';
        if (localStorage.getItem('mesToken') === 'undefined') {
          window.open(MES_URL);
        } else {
          // eslint-disable-next-line no-redeclare
          var tokenHeader = localStorage.getItem('mesTokenHeader');
          token = localStorage.getItem('mesToken');
          window.open(MES_URL + '?tokenHead=' + tokenHeader + '&token=' + token, token);
        }
        // const url = `${DNC_URL}?key=${token}`;
      } else if (navMenu.entity.menuName === '车间看板') {
        window.open('/kanban?cid=' + localStorage.getItem('companyId'));
      } else if (navMenu.entity.menuName === '安灯车间看板') {
        window.open('/kanban_andon?cid=' + localStorage.getItem('companyId'));
      } else {
        console.log(navMenu.entity.url);
        this.$router.push(navMenu.entity.url);
      }
    }
  },
  mounted() {
    // console.log(this.navMenus);
  }
};
</script>

<style lang="less" scoped>
// a标签不加下划线
a {
  text-decoration: none;
}

// 设置导航菜单高度为40px
.el-menu-item,
.el-submenu__title {
  text-align: left;
  height: 40px !important;
  line-height: 40px !important;
}

// 设置二级菜单边距：左边距为50px
.el-menu--inline .el-menu-item {
  padding: 0 0 0 50px !important;
  // 二级菜单字体不加粗
  .position {
    font-weight: normal;
  }
}

.el-menu-item{
  padding-left: 15px !important;
}

// 设置一级菜单边距：左边距为20px，右边距为15px
.el-submenu__title {
  padding: 0 15px 0 15px !important;
  text-align: left;
}

// 图片和文字间距，文字样式
.position {
  margin-left: 10px;
  font-size: 14px;
  font-weight: bold;
  font-stretch: normal;
  letter-spacing: 0px;
}

// 设置导航菜单收起后，二级菜单离收起后的边距（默认5px）
.el-menu--popup-right-start {
  margin-left: 2px !important;
}

// 给选中菜单添加背景颜色和左边框颜色
.el-menu-item.is-active {
  background-color: #333 !important;
  border-left: 5px solid #0871dc;
}

// 设置有菜单和没有子菜单收起后，图片居中显示
// el-tooltip设置的是没有子菜单
.el-menu--collapse .el-submenu__title,
.el-tooltip {
  padding: 0 0 0 10px !important;
}

// 设置导航菜单箭头图标颜色和大小
.el-submenu__title i {
  color: #fff !important;
  font-size: 18px;
}

// 菜单展开
.el-submenu.is-opened > .el-submenu__title .el-submenu__icon-arrow {
  -webkit-transform: rotateZ(90deg) !important;
  -ms-transform: rotate(90deg) !important;
  transform: rotateZ(90deg) !important;
}
</style>
