    //   }
    // }

    // if (this.getUrlKeyMain('hideHead', window.location.href) === 'true') {
    //   this.showHead = false;
    //   this.supplierShow = false;
    // }
    if (IMPLANT_PAGE) {
      this.showHead = false;
      this.supplierShow = false;
    } else {
      this.showHead = true;
      this.supplierShow = true;
    }

    // eslint-disable-next-line camelcase

