('use strict');
export function isNonemptyArray(arr) {
  if (Array.isArray(arr) && arr.length > 0) {
    return true;
  }

  return false;
}

export var ArrayUtil = /** @class */ (function() {
  function ArrayUtil() {}
  /**
   * 更新数组,若item已存在则将其从数组中删除,若不存在则将其添加到数组
   * **/
  ArrayUtil.updateArray = function(array, item) {
    for (var i = 0, len = array.length; i < len; i++) {
      var temp = array[i];
      if (item === temp) {
        array.splice(i, 1);
        return;
      }
    }
    array.push(item);
  };
  /**
   * 将数组中指定元素移除
   * @param array
   * @param item 要移除的item
   * @param id 要对比的属性，缺省则比较地址
   * @returns {*}
   */
  ArrayUtil.remove = function(array, item, id) {
    if (!array) return;
    for (var i = 0, l = array.length; i < l; i++) {
      var val = array[i];
      if (item === val || (val && val[id] && val[id] === item[id])) {
        array.splice(i, 1);
      }
    }
    return array;
  };
  /**
   * 判断两个数组的是否相等
   * @return boolean true 数组长度相等且对应元素相等
   * */
  ArrayUtil.isEqual = function(arr1, arr2) {
    if (!(arr1 && arr2)) return false;
    if (arr1.length !== arr2.length) return false;
    for (var i = 0, l = arr1.length; i < l; i++) {
      if (arr1[i] !== arr2[i]) return false;
    }
    return true;
  };
  /**
   * clone 数组
   * @return Array 新的数组
   * */
  ArrayUtil.clone = function(from) {
    if (!from) return [];
    var newArray = [];
    for (var i = 0, l = from.length; i < l; i++) {
      newArray[i] = from[i];
    }
    return newArray;
  };

  return ArrayUtil;
})();
