import Vue from 'vue';
import Vuex from 'vuex';
import { BASE_URL } from '@/utils/ajaxConfigure';
import pageInfo from '@/store/modules/pageInfo';
import menuList from '@/store/modules/menuList';
import print from '@/store/modules/print';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    dialogMarginClass: 'dialogWidth200',
    filters: {}, // group, repairTypeData, deviceNameData, userData
    repairStatus: [],
    levelStatus: [],
    formConfigDate: [
      { value: 1, label: 'yyyy-MM-dd HH:mm:ss' },
      { value: 2, label: 'yyyy-MM-dd' },
      { value: 3, label: 'HH:mm:ss' }
    ],
    timeStatus: [
      { type: 1, text: '天' },
      { type: 2, text: '周' },
      { type: 3, text: '月' },
      { type: 4, text: '半年' },
      { type: 5, text: '年' },
      { type: 6, text: '计划周期' }
    ],
    maintanceTemplate: [],
    maintainStatus: [],
    monitorTags: 0,
    op_ip: '',
    op_port: '',
    deviceIsJump: false, // 是否跳转
    deviceJump: {}, // 主控页跳转时存设备信息
    deviceInfoCode: '', // 设备配置公共存放code
    deviceInfoId: 0, // 设备配置公共存放id
    deviceManangeCode: '', // 设备管理公共存放code
    deviceManangeId: 0, // 设备管理公共存放id
    publicUrl: BASE_URL,
    workStationActivePage: null,
    templateRow: {}, // 保养手动排单-模版数据
    templateDevRow: {}, // 保养手动排单-设备数据
    templateDevCode: '', // 保养手动排单-设备数据
    materialRepair: {}, // 美康备件获取维修
    materialMaintance: {}, // 美康备件获取保养
    materialMkis: 1, // 1是维修 2是保养
    programMkis: 1 // 1是领料 2是退料
  },
  mutations: {
    setDialogMargin(state, param) {
      state.dialogMarginClass = param;
    },
    setFiltersData(state, payload) {
      state.filters[payload.name] = payload.data;
    },
    setDeviceInfoCode(state, param) {
      state.deviceInfoCode = param;
    },
    setDeviceInfoId(state, param) {
      state.deviceInfoId = param;
    },
    setWorkStationActivePage(state, param) {
      state.workStationActivePage = param;
    }
  },
  actions: {
  },
  modules: { pageInfo, menuList, print }
});
