import { postAplicationJSON, gettAplicationJSONMK, getAplicationJSONMK } from '@/utils/request';
import { SUPOS_URL, MK_IP } from '@/utils/ajaxConfigure';

// 验证蓝卓链接
export const suposAuthorize = (param) => gettAplicationJSONMK('http://122.112.160.41:8094/supos/' + 'supos/authorize?', param);
// 获取token验证
export const suposAccessToken = (param) => gettAplicationJSONMK('http://122.112.160.41:8094/supos/' + 'supos/accessToken?', param);
// 蓝卓获取token验证
export const authAccessToken = (param) => gettAplicationJSONMK(SUPOS_URL + 'auth/accessToken?', param);
// 美康token验证
export const loginMk = (param) => getAplicationJSONMK('http://' + MK_IP + ':8093/meikang/' + 'user/loginMk?', param);
// 验证token失效
export const openapiGetTokenOutTime = (param) => postAplicationJSON('open-api/getTokenOutTime.action?', param);
// 验证token 并 获取用户公司信息
export const openapiGetUserInfoByToken = (param) => postAplicationJSON('open-api/getUserInfoByToken.action?', param);
