import { getMyCustomMenuInfo, hasCustomMenu, menuGetList } from '@/api/base';
import { isNonemptyArray } from '@/utils/array';
// import { showErrorMessage } from '@/utils/message';
/**
 * 搜索Model的静态
 */

const menuList = {
  namespaced: true, // 命名空间
  // 初始化相关页面数据 否则第一个请求到数据不渲染
  state: {
    // 保存每个功能的状态，true代表属于常用功能
    mapCheckedStatusToId: [],
    // 构造页面的静态数据
    menuList: [],
    type: 'myInfo',
    activeName: '生产管理',
    treeData: []
  },
  mutations: {
    save(state, payload) {
      state.menuList = payload;
    },
    changeType(state, payload) {
      state.type = payload;
    },
    changeCheckedMenu(state, newData) {
      state.mapCheckedStatusToId = [...newData];
    },
    changeActiveName(state, payload) {
      state.activeName = payload;
    },
    changeTreeData(state, payload) {
      state.treeData = payload;
    }

  },
  actions: {
    /**
     * 获取菜单信息
     * @author ZHF
     */
    async getMenuList({ commit, state }) {
      try {
        hasCustomMenu({}).then(res => {
          console.log('666', res);
          if (res) {
            getMyCustomMenuInfo().then(myCustomMenu => {
              if (myCustomMenu.code === '0000') {
                commit('changeType', 'myCustomMenu');
                commit('changeTreeData', myCustomMenu.rows.authorityList);
              }
            });
          } else {
            commit('changeType', 'myInfo');
            commit('changeTreeData', []);
          }
        });

        const {
          rows: { authorityList },
          code
        } = await menuGetList({});
        if (code === '0000') {
          console.log(authorityList);
          if (isNonemptyArray(authorityList)) {
            if (authorityList.length === 0) {
              return [];
            }
            commit('changeCheckedMenu', []);
            const map = new Map();
            authorityList.forEach(model => {
              console.log('666', model);
              if (model.children) {
                const { entity, children } = model;
                const key = {
                  id: entity.id,
                  menuName: entity.menuName
                };

                const value = children.map(({ entity: childEntity }) => {
                  const isCommonFunc = childEntity.useFlag === 1;
                  var newData = [...state.mapCheckedStatusToId];
                  newData.push([childEntity.id, isCommonFunc]);
                  commit('changeCheckedMenu', newData);
                  return {
                    id: childEntity.id,
                    parentId: childEntity.parentId, // 用于遍历
                    menuName: childEntity.menuName,
                    path: childEntity.url,
                    useFlag: isCommonFunc
                  };
                });

                map.set(key, value);
              }
            });

            const menuList = map;
            commit('save', menuList);
          }
        } else {
          this.$router.replace({
            path: '/login'
          });
        }
      } catch (error) {
        // showErrorMessage(error);
      }
    }
  },
  getters: {}
};

export default menuList;
