<template>
  <div class="header">
    <div v-if="isShowLongLogo">
       <!-- <i v-show="showLogo()" class="icon-biglogo"></i>
       <img class="lzLogo" :src="require('../../../assets/img/TabBar/supos.png')"  /> -->
       <!-- <img :class="logoClass()" :src="logoImg()" /> -->
       <!-- <img class="header-logo" :src="logoImg()" /> -->
    </div>
    <div class="header-img" v-else>
      <i class="icon-smalllogo"></i>
    </div>
  </div>
</template>

<script>
// eslint-disable-next-line camelcase
import { IS_Mk, LOGO, LOGO_CONFIG } from '@/utils/ajaxConfigure';
export default {
  props: ['isShowLongLogo'],
  data() {
    return {
      // isShowLongLogo: true
    };
  },
  methods: {
    logoImg() {
      return require('../../../assets/img/Login/login_icon_Shattrath.png');
    },
    // logoImg() {
    //   var Logo = '';
    //   if (LOGO_CONFIG[localStorage.getItem('companyId')]) {
    //     Logo = LOGO_CONFIG[localStorage.getItem('companyId')].logo;
    //   } else {
    //     Logo = LOGO;
    //   }
    //   console.log(Logo, LOGO);
    //   if (Logo !== 'null') {
    //     return require('../../../assets/img/Login/nav_' + Logo + '.png');
    //   } else {
    //     return '';
    //   }
    // },
    logoClass() {
      if (LOGO === 'SUPOS') {
        return 'lzLogo';
      } else if (LOGO === 'YIY') {
        return 'yILogo';
      } else if (LOGO === 'SL') {
        return 'tyLogo';
      } else if (LOGO === 'BY') {
        return 'byLogo';
      } else {
        if (LOGO_CONFIG[localStorage.getItem('companyId')]) {
          var logo = LOGO_CONFIG[localStorage.getItem('companyId')].logo;
          if (logo === 'LT') {
            return 'ltLogo';
          } else if (logo === 'SUPOS') {
            return 'lzLogo';
          } else if (logo === 'YIY') {
            return 'yILogo';
          } else if (logo === 'SL') {
            return 'tyLogo';
          } else if (logo === 'BY') {
            return 'byLogo';
          } else {
            return '';
          }
        } else {
          return '';
        }
      }
    },
    showLogo() {
      // eslint-disable-next-line camelcase
      if (IS_Mk) {
        return false;
      }
      if (localStorage.getItem('isSupplier') === 'false') {
        return true;
      } else {
        return false;
      }
    },
    /**
     * @description: more点击，后把isShowLongLogo值取反传给父组件
     * @param {}
     * @return:
     * @author: YZZ
     */
    onIsCollapse() {
      this.$emit('onIsCollapse', !this.isShowLongLogo);
    }
  }
};
</script>

<style lang="less" scoped>
  .header {
    .header-logo {
      width: 118px;
      margin-right: 20px;
    }
  }

  .header-img {
    height: 40px;
    width: 200px;
    position: relative;

    font-size: 30px;
    color: #03a9f4;
    font-family: unset;
  }

  .header-more {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    border-left: 1px solid #c6e1fa;
  }

  .lzLogo{
    width: 120px;
    height: 35px;
    display: inline-block;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -40%);
  }
  .yILogo{
    width: 100px;
    height: 35px;
    display: inline-block;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -40%);
  }

  .tyLogo{
    width: 100%;
    height: 120%;
    margin-top: -6px;
  }

  .byLogo{
    width: 85%;
    height: 120%;
    margin-top: 0px;
    margin-left: -10px;
  }

  .ltLogo{
    width: 90%;
    height: 110%;
    margin-top: 0px;
  }
</style>
