import { get, postParamJSON } from '@/utils/request';

// 根据页面获取搜索框中的field和type的下拉选项
export const getDropDownListForSearchAPI = async query => {
  const res = await postParamJSON('/configure/pageSearch/loadPageSearch.action?', query, {});
  res.characterDropDown2 = res.characterDropDown;
  res.characterDropDown = res.characterDropDown.string;
  return new Promise((resolve, reject) => {
    resolve(res);
  });
};

// 传入api获取简单搜索的下拉选项
export const getDropDownListByAPI = async url => get(
  url,
  {},
  {}
);

// 创建搜索记录
export const createSearchRecordAPI = (query, body) =>
  postParamJSON(
    '/configure/pageSearchHis/createPageSearchHis.action?',
    query,
    body
  );

// 根据id删除搜索记录
export const deleteSearchRecordAPI = query =>
  postParamJSON('/configure/pageSearchHis/removeById.action?', query, {});

// 根据id获取搜索记录详情
export const getSearchRecordDetailAPI = query =>
  postParamJSON(
    '/configure/pageSearchHis/getPageSearchHisDetail.action?',
    query,
    {}
  );

// 获取某个页面的搜索记录列表
export const getSearchRecordListAPI = query =>
  postParamJSON(
    '/configure/pageSearchHis/getPageSearchHisList.action?',
    query,
    {}
  );

// 根据页面的url获取对应的menuId
export const getMenuIdForUrlAPI = query =>
  postParamJSON('/configure/menu/getMenuIdByUrl.action?', query, {});

// 点击搜索按钮时，记录搜索日志
export const recordSearchLogAPI = (head, body) =>
  postParamJSON('/configure/pageSearchHis/recordSearchLog.action?', head, body);
// 获取搜索inputs
export const getShowListAPI = head =>
  postParamJSON('/configure/pageSearchHis/getShowList.action?', head, {});
// 获取对应搜索记录的搜索inputs
export const getShowListByIdAPI = head =>
  postParamJSON('/configure/pageSearchHis/getShowListById.action?', head, {});

// 点击搜索的输入框时，记录搜索日志
export const getSearchLogAPI = (head = {}, body = {}) =>
  postParamJSON('/configure/pageSearchHis/getSearchLog.action?', head, body);
