import moment from 'moment';
import '@/utils/dateFormat';
import { VARIABLE, MAINTAINCE_CYCLE_TYPE, WEEK_DAY, EQUIPMENT_DATA } from '@/utils/constantsFile';
import store from '@/store';
export default {
  // 时间匹配过滤器
  formatTime(time) {
    if (time) {
      var processTime = time.cellValue ? time.cellValue : time;
      return moment(processTime).format('YYYY-MM-DD HH:mm:ss');
    } else {
      return null;
    }
  },
  // 计算全程时间过滤器
  formatTotalTime(time1, time2) {
    if (time1 && time2) {
      var t1 = moment(time1);
      var t2 = moment(time2);
      var dura = t2.format('x') - t1.format('x');
      var tempTime = moment.duration(dura);
      return tempTime.days();
    }
  },
  // 间隔天数标准化
  formatDifferentTime(time) {
    console.log(time);
    if (time) {
      var data = time.cellValue;
      // var days = parseInt(time.cellValue / 24);
      // var hours = time.cellValue - parseInt(time.cellValue / 24) * 24;
      // var str = days + VARIABLE.DAY;
      // if (hours !== 0) {
      //   str = str + hours + VARIABLE.HOUR;
      // }
      // 计算出相差天数
      var days = Math.floor(data / (24 * 3600));
      // 计算出小时数
      var leave1 = data % (24 * 3600);
      // 计算天数后剩余的毫秒数
      var hours = Math.floor(leave1 / 3600);
      // 计算相差分钟数
      var leave2 = leave1 % 3600;
      // 计算小时数后剩余的毫秒数
      var minutes = Math.floor(leave2 / 60);
      var str = days ? days + VARIABLE.DAY : null;
      str = hours ? str + hours + VARIABLE.HOUR_NAME : str;
      str = minutes ? str + minutes + VARIABLE.MINUTE_NAME : str;
      if (str === null) {
        str = '<1分钟';
      }
      return str;
    }
  },
  formatDifferentTime2(time) {
    console.log(time);
    if (time) {
      var data = time.cellValue;
      // var days = parseInt(time.cellValue / 24);
      // var hours = time.cellValue - parseInt(time.cellValue / 24) * 24;
      // var str = days + VARIABLE.DAY;
      // if (hours !== 0) {
      //   str = str + hours + VARIABLE.HOUR;
      // }
      // 计算出相差天数
      var days = Math.floor(data / (24 * 3600));
      // 计算出小时数
      var leave1 = data % (24 * 3600);
      // 计算天数后剩余的毫秒数
      var hours = Math.floor(leave1 / 3600);
      // 计算相差分钟数
      var leave2 = leave1 % 3600;
      // 计算小时数后剩余的毫秒数
      var minutes = Math.floor(leave2 / 60);
      var str = days ? days + VARIABLE.DAY : null;
      str = hours ? str + hours + VARIABLE.HOUR_NAME : str;
      str = minutes ? str + minutes + VARIABLE.MINUTE_NAME : str;
      if (str === null) {
        str = '<1分钟';
      } else {
        str = Math.floor(data / 60) + VARIABLE.MINUTE_NAME;
      }
      return str;
    }
  },
  // 级别过滤器
  formatMaintanceLevel(data) {
    if (data && store.state.filters.MaintainLevel) {
      var processData = data.cellValue ? data.cellValue : data;
      var res = null;
      for (var i = 0; i < store.state.filters.MaintainLevel.length; i++) {
        if (store.state.filters.MaintainLevel[i].basicId === processData) {
          res = store.state.filters.MaintainLevel[i].basicValue;
        }
      }
      return res;
    }
  },
  // 周期过滤器
  formatMaintanceCycle(data) {
    if (data) {
      var processData = data.cellValue ? data.cellValue : data;
      return MAINTAINCE_CYCLE_TYPE[processData];
    }
  },
  // 构建保养时间过滤器
  formatMaintanceTime(data) {
    if (data) {
      var str = EQUIPMENT_DATA.EACH;
      var item = data.row;
      switch (item.cycleType) {
        case 1:
          // 天
          str = str + item.typeSeparate + MAINTAINCE_CYCLE_TYPE[item.cycleType];
          break;
        case 2:
          // 周
          str = str + item.typeSeparate + MAINTAINCE_CYCLE_TYPE[item.cycleType];
          if (item.typeWeekCheckWeekDay) {
            var weekdays = item.typeWeekCheckWeekDay.split(',');
            var weeks = [];
            weekdays.forEach((weekday) => {
              weeks.push(WEEK_DAY[weekday]);
            });
            str = str + EQUIPMENT_DATA.AND + weeks.toString();
          }
          break;
        case 3:
          str = str + item.typeSeparate + MAINTAINCE_CYCLE_TYPE[item.cycleType];
          break;
        case 4:
          str = str + MAINTAINCE_CYCLE_TYPE[item.cycleType];
          break;
        case 5:
          str = str + item.typeSeparate + MAINTAINCE_CYCLE_TYPE[item.cycleType];
          break;
        case 6:
          str = '自定义周期';
          break;
      }
      if (item.cycleType === 6) {
        return str;
      } else {
        str = str + EQUIPMENT_DATA.MAINTANCE_ONCE;
        return str;
      }
    }
  },
  // 场景班次过滤器
  formatShiftTime({ cellValue }) {
    const arr = [];
    for (var i in cellValue) {
      arr.push(cellValue[i].shiftName + ':' + cellValue[i].shiftStartTime + '~' + cellValue[i].shiftEndTime);
    }
    return arr.join(' ');
  },
  // 联级器倒推获取ids
  getCascaderIds(value, list) {
    const ids = [value];
    const matchCascaderData = (list) => {
      list.forEach(item => {
        if (item.id === ids[0]) {
          if (item.pId > 0) {
            ids.unshift(item.pId);
            matchCascaderData(list);
          }
        }
      });
    };
    matchCascaderData(list);
    return ids;
  },
  // 高亮样式
  formatHighlightValue(data, keyvalue) {
    var classnm = null;
    if (data) {
      if (data === keyvalue) {
        classnm = 'red-font';
      }
    }
    return classnm;
  },
  // 扩展字段的不同类型在表单中的验证
  formatterFormValidate(rule, value, callback, list) {
    var regPos = /^\d+(\.\d+)?$/; // 非负浮点数
    var regNeg = /^(-(([0-9]+\.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*\.[0-9]+)|([0-9]*[1-9][0-9]*)))$/; // 负浮点数
    var num = Number(rule.field.split('.')[1]);
    var field = list[num];
    value = list[num].extendValue;

    if (value === undefined) {
      if (field.propertyIsNull === 1) {
        return callback(new Error('请输入内容'));
      } else {
        return callback();
      }
    }
    // 若是字符
    if (field.propertyType === 1) {
      // 若必填并且未输入值
      if (field.propertyIsNull === 1 && (value === '' || value === null)) {
        return callback(new Error('请输入内容'));
      } else if (value && value.length > field.propertyLength) { // 若值长度大于字符长度
        return callback(new Error('长度范围不能超过' + field.propertyLength));
      } else {
        return callback();
      }
    } else if (field.propertyType === 2) { // 若是数字
      if (!regPos.test(value) && !regNeg.test(value)) { // 若值不是数字
        if (value) {
          return callback(new Error('必须为数字'));
        } else if (field.propertyIsNull === 1 && !value) {
          return callback(new Error('请输入内容'));
        } else {
          return callback();
        }
      } else if (value.indexOf('.') !== -1) { // 若值包含小数点
        var numa = value.split('.')[0];
        var float = value.split('.')[1];
        if (numa.length > field.propertyLength) { // 若小数点前缀大于长度
          return callback(new Error('长度范围不能超过' + field.propertyLength));
        } else if (float.length > field.propertyLength) { // 若小数点后缀大于小数点位
          return callback(new Error('小数范围不能超过' + field.propertyLength));
        } else {
          return callback();
        }
      } else {
        // 若值超出长度
        if (value.length > field.propertyLength) {
          return callback(new Error('长度范围不能超过' + field.propertyLength));
        } else {
          return callback();
        }
      }
    } else if (field.propertyType === 3) { // 若是日期
      // 若未必填并且未输入值
      if (field.propertyIsNull === 1 && !value) {
        return callback(new Error('请输入内容'));
      } else {
        return callback();
      }
    }
  },
  formatterTableValidate(rule, value, list, resolve, reject) {
    var regPos = /^\d+(\.\d+)?$/; // 非负浮点数
    var regNeg = /^(-(([0-9]+\.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*\.[0-9]+)|([0-9]*[1-9][0-9]*)))$/; // 负浮点数
    var field = list[rule.className];

    // 若是字符
    if (field.propertyType === 1) {
      // 若必填并且未输入值
      if (value.length > field.propertyLength) { // 若值长度大于字符长度
        return reject(new Error('长度范围不能超过' + field.propertyLength));
      } else {
        return resolve();
      }
    } else if (field.propertyType === 2) { // 若是数字
      if (!regPos.test(value) && !regNeg.test(value)) { // 若值不是数字
        if (field.propertyIsNull === 1 && value === '') {
          return reject(new Error('必须为数字'));
        } else {
          return resolve();
        }
      } else if (value.indexOf('.') !== -1) { // 若值包含小数点
        var numa = value.split('.')[0];
        var float = value.split('.')[1];
        if (numa.length > field.propertyLength) { // 若小数点前缀大于长度
          return reject(new Error('长度范围不能超过' + field.propertyLength));
        } else if (float.length > field.propertyLength) { // 若小数点后缀大于小数点位
          return reject(new Error('小数范围不能超过' + field.propertyLength));
        } else {
          return resolve();
        }
      } else {
        // 若值超出长度
        if (value.length > field.propertyLength) {
          return reject(new Error('长度范围不能超过' + field.propertyLength));
        } else {
          return resolve();
        }
      }
    } else { // 若是日期
      return resolve();
    }
  },
  /**
   * @description: 扩展字段格式化，显示列表扩展字段的值
   * @param row 行数据
   * @param column 列参数
   * @return: void
   * @author: ZJM
   */
  formatterExtendValue({ row, column }) {
    var str = '';
    for (var item in row.extendPropertyValueVoList) {
      if (column.property === row.extendPropertyValueVoList[item].propertyCode) {
        if (row.extendPropertyValueVoList[item].extendValue !== undefined) {
          str = row.extendPropertyValueVoList[item].extendValue;
        }
      }
    }
    return str;
  },
  // 将编码转换成字符
  utf8ToChar(str) {
    var iCode, iCode1, iCode2;
    iCode = parseInt('0x' + str.substr(1, 2));
    iCode1 = parseInt('0x' + str.substr(4, 2));
    iCode2 = parseInt('0x' + str.substr(7, 2));
    return String.fromCharCode(
      ((iCode & 0x0f) << 12) | ((iCode1 & 0x3f) << 6) | (iCode2 & 0x3f)
    );
  },
  // 将 URL中的UTF-8字符串转成中文字符串
  getCharFromUtf8(str) {
    var cstr = '';
    var nOffset = 0;
    if (str === '') return '';
    str = str.toLowerCase();
    nOffset = str.indexOf('%e');
    if (nOffset === -1) return str;
    while (nOffset !== -1) {
      cstr += str.substr(0, nOffset);
      str = str.substr(nOffset, str.length - nOffset);
      if (str === '' || str.length < 9) return cstr;
      cstr += this.utf8ToChar(str.substr(0, 9));
      str = str.substr(9, str.length - 9);
      nOffset = str.indexOf('%e');
    }
    return cstr + str;
  },
  setExportFunc(res, fileName) {
    debugger;
    const content = res.data;
    // FileReader 对象允许Web应用程序异步读取存储在用户计算机上的文件的内容
    if (!fileName) {
      if (res.headers['content-disposition']) {
        fileName = res.headers['content-disposition'].split(';')[1];
      }
      fileName = this.getCharFromUtf8(
        fileName.substring(fileName.lastIndexOf('=') + 1, fileName.length)
      );
    }
    var blob = new Blob([content], { type: 'application/excel' });

    if ('download' in document.createElement('a')) {
      // 支持a标签download的浏览器
      const link = document.createElement('a');
      // 创建a标签
      link.download = fileName;
      // a标签添加属性
      link.style.display = 'none';
      link.href = URL.createObjectURL(blob);
      document.body.appendChild(link);
      link.click();
      // 执行下载
      URL.revokeObjectURL(link.href);
      // 释放url
      // document.body.removeChild(link);
      // 释放标签
    } else {
      // 其他浏览器
      navigator.msSaveBlob(blob, fileName);
    }
  },
  // 数字相加
  computedAdd() {
    var args = arguments;// 获取所有的参数
    // var lens = args.length;// 获取参数的长度
    var d = 0;// 定义小数位的初始长度，默认为整数，即小数位为0
    var sum = 0;// 定义sum来接收所有数据的和
    // 循环所有的参数
    for (var key in args) { // 遍历所有的参数
      // 把数字转为字符串
      var str = '' + args[key];
      if (str.indexOf('.') !== -1) { // 判断数字是否为小数
        // 获取小数位的长度
        var temp = str.split('.')[1].length;
        // 比较此数的小数位与原小数位的长度，取小数位较长的存储到d中
        d = d < temp ? temp : d;
      }
    }
    // 计算需要乘的数值
    var m = Math.pow(10, d);
    // 遍历所有参数并相加
    for (var keyx in args) {
      sum += args[keyx] * m;
    }
    // 返回结果
    return sum / m;
  },
  // 数字相减
  accSubtr(arg1, arg2) {
    var r1, r2, m, n;
    try { r1 = arg1.toString().split('.')[1].length; } catch (e) { r1 = 0; }
    try { r2 = arg2.toString().split('.')[1].length; } catch (e) { r2 = 0; }
    m = Math.pow(10, Math.max(r1, r2));
    // 动态控制精度长度
    n = (r1 >= r2) ? r1 : r2;
    return ((arg1 * m - arg2 * m) / m).toFixed(n);
  },
  // 校验正数Form
  positiveNumber(rule, value, callback) {
    var x = /^\d+(?=\.{0,1}\d+$|$)/;// 正数
    if (!x.test(value)) {
      return callback(new Error('请输入正数'));
    } else if (Number(value) > 99999999 || Number(value) < 0) {
      return callback(new Error('请输入范围0-99999999的数字'));
    } else if (isNaN(value)) {
      return callback(new Error('请输入正数'));
    } else {
      return callback();
    }
  },
  // 校验正数Table
  positiveNumberTable(rule, value, resolve, reject) {
    var x = /^\d+(?=\.{0,1}\d+$|$)/;// 正数
    if (!x.test(value)) {
      return reject(new Error('请输入正数'));
    } else if (Number(value) > 99999999 || Number(value) < 0) {
      return reject(new Error('请输入范围0-99999999的数字'));
    } else if (isNaN(value)) {
      return reject(new Error('请输入正数'));
    } else {
      return resolve();
    }
  },
  getUrlKey(name, url) {
    // eslint-disable-next-line no-sparse-arrays
    return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(url) || [, ''])[1].replace(/\+/g, '%20')) || null;
  },
  // 计算列表高度
  getTableHeight(num) {
    // eslint-disable-next-line no-sparse-arrays
    // return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(url) || [, ''])[1].replace(/\+/g, '%20')) || null;
    return document.documentElement.clientHeight - num;
  },
  // 计算列表高度，百分比
  getTableHeightBfb(num) {
    // eslint-disable-next-line no-sparse-arrays
    // return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(url) || [, ''])[1].replace(/\+/g, '%20')) || null;
    return document.documentElement.clientHeight * num;
  }
};
