const pageInfo = {
  namespaced: true, // 命名空间
  state: {
    menuList: [],
    pageConfigInfo: {
      yieldKey: '产量',
      isUseDeviceManage: 1
    },
    8: {}, // 用户管理
    9: {}, // 权限管理
    10: {}, // 设备配置
    11: {}, // 告警配置
    12: {}, // 质检配置——未设
    13: {}, // 保养配置
    14: {}, // 点检配置
    16: {}, // 编码配置
    17: {}, // 基础信息配置
    18: {}, // 表单配置
    19: {}, // 流程配置
    33: {}, // 设备实况
    35: {}, // 保养任务
    36: {}, // 维修任务
    37: {}, // 工具提醒
    38: {}, // 设备点检
    22: {}, // 设备监控报表
    23: {}, // 设备管理报表
    25: {}, // 设备数据对比
    79: {}, // 设备产量报表
    81: {}, // 设备报警监控
    86: {}, // 设备状态报表
    89: {}, // 设备空闲报表
    130: {}, // 备件统计报表
    131: {}, // 保养计划报表
    132: {}, // 保养年度统计报表
    // andon
    148: {}, // 安灯状态分布
    149: {}, // 安灯设备状态报表
    // 一元
    160: {}, // 设备管理
    161: {}, // 设备管理
    169: {}, // 设备管理
    150: {}, // 安灯 设备实况
    152: {}, // 安灯 设备实况
    170: {} // 安灯 设备实况
  },
  mutations: {
    save(state, payload) {
      state[payload.id] = {};
      state[payload.id] = payload;
      // refreshState(state[payload.id], payload);
    },
    setMenuData(state, payload) {
      state.menuList = payload;
    },
    changeCssType(state, cssType = 1) {
      const { id } = getUrlParams();
      state[id].currentCssType = cssType;
    },
    setYieldKey(state, value) {
      state.pageConfigInfo.yieldKey = value;
      const pageConfigInfo = JSON.parse(localStorage.getItem('pageConfigInfo'));
      if (pageConfigInfo) {
        pageConfigInfo.yieldKey = value;
        localStorage.setItem('pageConfigInfo', JSON.stringify(pageConfigInfo));
      } else {
        localStorage.setItem(
          'pageConfigInfo',
          JSON.stringify({
            yieldKey: value
          })
        );
      }
    },
    setIsUseDeviceManage(state, value) {
      state.pageConfigInfo.isUseDeviceManage = value;
      const pageConfigInfo = JSON.parse(localStorage.getItem('pageConfigInfo'));
      if (pageConfigInfo) {
        pageConfigInfo.isUseDeviceManage = value;
        localStorage.setItem('pageConfigInfo', JSON.stringify(pageConfigInfo));
      } else {
        localStorage.setItem(
          'pageConfigInfo',
          JSON.stringify({
            isUseDeviceManage: value
          })
        );
      }
    }
  },
  menuNameFunc(param) {
    if (param.indexOf('产量') > -1) {
      return param.replace('产量', pageInfo.state.pageConfigInfo.yieldKey);
    }
    if (GAS_NAME !== '气') {
      if (param.indexOf('气') > -1) {
        return param.replace('气', GAS_NAME);
      } else {
        return param;
      }
    } else {
      return param;
    }
  },
  actions: {
    // 获取当前页面的信息 包括按钮 menu等信息
    // async queryPageInfo({ commit, state }) {
    // const { id } = getUrlParams();
    // const res = await queryPageInfoAPI({ id });
    // const res = {
    //   id: 33,
    //   url: '/warehouse/productIntoStock',
    //   menuName: '生产入库',
    //   menuCode: 'SCRK',
    //   init: 1,
    //   buttonList: [{
    //     id: 65,
    //     code: 'SCRK_CREATE',
    //     cssType: 1,
    //     rank: '1',
    //     url: '',
    //     buttonName: '生成入库单',
    //     action: 'clickCreateBillButton',
    //     icon: 'el-icon-document-add'
    //   }]
    // };

    // commit('save', { ...res });
    //   commit('save', {});
    // }

    // 获取菜单 按钮数据
    async getMenuButton({ commit }) {
      const res = await getCommonUseFunctions({});
      const collectMenuId = res.rows.map(item => item.menuId);
      return new Promise((resolve, reject) => {
        userGetListUserPowerAction().then(res => {
          const data = res.rows;
          var arr = [];
          for (var i in data) {
            var json = { entity: {} };
            json.entity.rank = data[i].entity.menuParentId;
            json.entity.menuName = pageInfo.menuNameFunc(
              data[i].entity.powerCnName
            );
            json.entity.id = data[i].entity.menuId;
            json.entity.parentId = data[i].entity.menuParentId;
            json.entity.url =
              data[i].entity.url + '?id=' + data[i].entity.menuId;
            json.entity.icon = data[i].entity.icon;
            json.entity.extend02 = data[i].entity.extend02;
            if (data[i].children.length > 0) {
              var datax = data[i].children;
              var arrx = [];
              for (var x in datax) {
                var jsonx = { entity: {} };
                var jsony = {};
                jsonx.entity.rank = datax[x].menuParentId;
                jsonx.entity.menuName = pageInfo.menuNameFunc(
                  datax[x].powerCnName
                );
                jsonx.entity.id = datax[x].menuId;
                jsonx.entity.parentId = datax[x].menuParentId;

                if (jsonx.entity.menuName.indexOf('IMP') > -1 || jsonx.entity.menuName.indexOf('SCADA') > -1) {
                  jsonx.entity.url = datax[x].url;
                } else {
                  jsonx.entity.url = datax[x].url + '?id=' + datax[x].menuId;
                }
                // jsonx.entity.url = datax[x].url + '?id=' + datax[x].menuId;

                jsonx.entity.icon = datax[x].icon;
                jsonx.entity.useFlag = collectMenuId.indexOf(datax[x].menuId) < 0 ? 0 : 1;
                jsony.id = datax[x].menuId;
                jsonx.entity.extend02 = datax[x].extend02;

                if (jsonx.entity.menuName.indexOf('IMP') > -1 || jsonx.entity.menuName.indexOf('SCADA') > -1) {
                  jsony.url = datax[x].url;
                } else {
                  jsony.url = datax[x].url + '?id=' + datax[x].menuId;
                }

                jsony.menuCode = datax[x].powerEnName;
                jsony.buttonList = datax[x].hasButton;
                commit('save', jsony);
                // that.$store.state.pageInfo[datax[x].menuId] = jsony;
                arrx.push(jsonx);
              }
              json.children = arrx;
            }
            arr.push(json);
          }
          commit('setMenuData', arr);
          resolve();
        });
      });
    },
    // 获取生产配置
    async getYieldConfig({ commit }) {
      try {
        const data = await configurationGetConfigurationByEnum(
          {
            configurationTypeEnum: 'PRODUCTION_CONFIGURATION'
          },
          {}
        );
        if (data.code === '0000') {
          commit('setYieldKey', data.rows?.processConfiguration || '产量');
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    // 是否启用设备管理配置
    async getDeviceMangeConfig({ commit }) {
      try {
        const data = await configurationGetConfigurationByEnum(
          {
            configurationTypeEnum: 'ENABLE_DEVICE_MANAGEMENT'
          },
          {}
        );
        if (data.code === '0000') {
          commit('setIsUseDeviceManage', data.rows?.isEnable ?? 1);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    }
  },
  getters: {}
};

export default pageInfo;
