import axios from 'axios';

import { PRINT_URL } from '@/utils/ajaxConfigure';

import '@/utils/dateFormat';

/**
 * @description: 自定义默认值
 * @param {type}
 * @return:
 * @author: YZZ
 */

const instance = axios.create({
  // 设置前缀
  baseURL: PRINT_URL,
  // 设置超时
  timeout: window.global_config.MAX_WAITING_TIME
});

instance.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded';

export function postPrint(url, jsonData, config) {
  return new Promise((resolve, reject) => {
    instance
      .post(url, jsonData, config)
      .then(res => {
        console.log(url);
        console.log(jsonData);
        console.log(config);
        resolve(res.data);
      })
      .catch(err => {
        reject(err.data);
      });
  });
}
