import CryptoJS from 'crypto-js';
// import { LOGIN_KEY } from '@/utils/ajaxConfigure';
export default {
  encryptText(inputText, secretKey) {
    const encryptedData = CryptoJS.DES.encrypt(inputText, secretKey).toString();
    return encryptedData;
  },
  decryptText(encryptedText, secretKey) {
    try {
      const decryptedData = CryptoJS.DES.decrypt(encryptedText, secretKey).toString(CryptoJS.enc.Utf8);
      return decryptedData;
    } catch (error) {
      return '解密失败，可能是密钥不正确或加密数据损坏。';
    }
  },
  // 加密
  encrypt(word, keyStr) {
    // 设置一个默认值，如果第二个参数为空采用默认值，不为空则采用新设置的密钥
    // keyStr = keyStr || LOGIN_KEY;
    var key = CryptoJS.enc.Utf8.parse(keyStr);
    var srcs = CryptoJS.enc.Utf8.parse(word);
    var encrypted = CryptoJS.AES.encrypt(srcs, key, { mode: CryptoJS.mode.ECB, padding: CryptoJS.pad.Pkcs7 });
    return encrypted.toString();
  },
  // 解密
  decrypt(word, keyStr) {
    // keyStr = keyStr || LOGIN_KEY;
    var key = CryptoJS.enc.Utf8.parse(keyStr);
    var decrypt = CryptoJS.AES.decrypt(word, key, { mode: CryptoJS.mode.ECB, padding: CryptoJS.pad.Pkcs7 });
    return CryptoJS.enc.Utf8.stringify(decrypt).toString();
  },
  // 加密
  encryptDes(message, keyStr) {
    // var keyHex = CryptoJS.enc.Utf8.parse(keyStr);
    // var option = { mode: CryptoJS.mode.ECB, padding: CryptoJS.pad.Pkcs7 };
    // var encrypted = CryptoJS.DES.encrypt(message, keyHex, option);
    // return encrypted.ciphertext.toString();
    var keyHex = CryptoJS.enc.Utf8.parse(keyStr);
    var encrypted = CryptoJS.DES.encrypt(message, keyHex, {
      mode: CryptoJS.mode.ECB,
      padding: CryptoJS.pad.Pkcs7
    });
    return encrypted.toString();
  },
  // 解密
  decryptDes(message, keyStr) {
    debugger;
    var keyHex = CryptoJS.enc.Utf8.parse(keyStr);
    var decrypted = CryptoJS.DES.decrypt({ ciphertext: CryptoJS.enc.Hex.parse(message) }, keyHex, { mode: CryptoJS.mode.ECB, padding: CryptoJS.pad.Pkcs7 });
    console.log(decrypted.toString());
    console.log(decrypted.toString(CryptoJS.enc.Utf8));
    return decrypted.toString(CryptoJS.enc.Utf8);
  },
  encryptDes2(word, keyStr, ivStr) {
    // let key = KEY;
    // let iv = IV;
    // if (keyStr) {
    var key = CryptoJS.enc.Utf8.parse(keyStr);
    var iv = CryptoJS.enc.Utf8.parse(ivStr);
    // }
    var srcs = CryptoJS.enc.Utf8.parse(word);
    var encrypted = CryptoJS.AES.encrypt(srcs, key, {
      iv: iv,
      mode: CryptoJS.mode.CBC,
      padding: CryptoJS.pad.ZeroPadding
    });
    return CryptoJS.enc.Base64.stringify(encrypted.ciphertext);
  }
};
