export function isAssetTypeAnImage(ext) {
  return (
    ['png', 'jpg', 'jpeg', 'bmp', 'gif', 'webp', 'psd', 'svg', 'tiff'].indexOf(
      ext.toLowerCase()
    ) !== -1
  );
}
/**
 * 判断某附件是否为图片格式
 * @param name 文件名称
 * @returns {boolean}
 */
export function isImg(name) {
  var fileName = name.lastIndexOf('.'); // 取到文件名开始到最后一个点的长度

  var fileNameLength = name.length; // 取到文件名长度

  var fileFormat = name.substring(fileName + 1, fileNameLength); // 截
  var imgArr = [
    'bmp',
    'jpg',
    'png',
    'tif',
    'gif',
    'pcx',
    'tga',
    'exif',
    'fpx',
    'svg',
    'psd',
    'cdr',
    'pcd',
    'dxf',
    'ufo',
    'eps',
    'ai',
    'raw',
    'WMF',
    'webp'
  ];
  return imgArr.indexOf(fileFormat) !== -1;
}
