/**
 * 获取url的参数
 */
export function getUrlParams(url = window.location.href) {
  const theRequest = {};
  if (url.indexOf('?') !== -1) {
    const str = url.split('?')[1];
    const strs = str.split('&');
    for (let i = 0; i < strs.length; i++) {
      theRequest[strs[i].split('=')[0]] = decodeURI(strs[i].split('=')[1]);
    }
  }
  return theRequest;
}

/**
 * @description: 点击“出库数量”一列的input时，自动选中input中的内容
 * @param {object} row 当前行
 * @param {object} event 事件对象
 * @author: cyb
 */
export function selectContent({ row }, event) {
  event.currentTarget.select();
}
/**
 * @description: 递归请求 异步变同步
 * @param {*} API
 * @param {*} params
 * @param {*} len
 * @param {*} i
 * @return {*}
 * @author: ZHF
 */
export function iterator(API, params, len) {
  if (len > 0) {
    API('', params).then(res => {
      iterator(API, params, len - 1);
    });
  }
}
/**
 *解析url参数
 * @param {*} name
 * @param {*} url
 */
export function getUrlKey(variable) {
  var query = window.location.search.substring(1);
  // console.log(query, window.location.search);
  var vars = query.split('&');
  // console.log(vars);
  for (var i = 0; i < vars.length; i++) {
    var pair = vars[i].split('=');
    if (pair[0] === variable) {
      // console.log(pair[1]);
      return pair[1];
    }
  }
  return false;
}

/**
 ** 乘法函数，用来得到精确的乘法结果
 ** 说明：javascript的乘法结果会有误差，在两个浮点数相乘的时候会比较明显。这个函数返回较为精确的乘法结果。
 ** 调用：accMul(arg1,arg2)
 ** 返回值：arg1乘以 arg2的精确结果
 **/
export function accMul(arg1, arg2) {
  let m = 0;
  const s1 = arg1.toString();
  const s2 = arg2.toString();
  try {
    m += s1.split('.')[1].length;
  } catch (e) {}
  try {
    m += s2.split('.')[1].length;
  } catch (e) {}
  return (
    (Number(s1.replace('.', '')) * Number(s2.replace('.', ''))) /
    Math.pow(10, m)
  );
}

/**
 * 根据屏幕尺寸改变字体大小
 * @param { Number } num
 * @returns { Number }
 */
export const fontSize = (num = 10) => {
  // let clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth
  const clientheight =
    window.innerHeight ||
    document.documentElement.clientHeight ||
    document.body.clientHeight;
  // if (!clientWidth) return
  // let fontSize = clientWidth / 1920
  if (!clientheight) return;
  const fontSize = clientheight / 1080;
  return num * fontSize;
};

/**
 * Parse the time to string(时间转换字符串)
 * @param {(Object|string|number)} time
 * @param {string} cFormat
 * @returns {string}
 */
export function parseTime(time, cFormat) {
  if (arguments.length === 0) {
    return null;
  }
  const format = cFormat || '{y}-{m}-{d} {h}:{i}:{s}';
  let date;
  if (typeof time === 'object') {
    date = time;
  } else {
    if (typeof time === 'string' && /^[0-9]+$/.test(time)) {
      time = parseInt(time);
    }
    if (typeof time === 'number' && time.toString().length === 10) {
      time = time * 1000;
    }
    date = new Date(time);
  }
  const formatObj = {
    y: date.getFullYear(),
    m: date.getMonth() + 1,
    d: date.getDate(),
    h: date.getHours(),
    i: date.getMinutes(),
    s: date.getSeconds(),
    a: date.getDay()
  };
  const timeStr = format.replace(/{(y|m|d|h|i|s|a)+}/g, (result, key) => {
    let value = formatObj[key];
    // Note: getDay() returns 0 on Sunday
    if (key === 'a') {
      return ['日', '一', '二', '三', '四', '五', '六'][value];
    }
    if (result.length > 0 && value < 10) {
      value = '0' + value;
    }
    return value || 0;
  });
  return timeStr;
}
