 <template>
  <div style="" id="headerSearch">
    <div class="header">
      <div class="header-left-border">
        <!--<div>
        <span class="head-title-span">{{ title }}</span>
      </div>-->
        <!-- <div class="head-search"> -->
        <!--<i class="icon-search"></i>-->
        <!-- <span class="search-span">快速查询</span> -->
        <!-- <span class="search-span cursor-pointer">快速查询</span> -->
        <!-- </div> -->
        <div v-if="showPrimarySearch === true" class="primary-search">
          <!-- <div
          v-if="showPrimarySearch === true"
          class="margin-left-20 primary-search"
        > -->
          <el-select
            v-model="primarySearchField"
            class="width-150 gap-10"
            size="small"
            @change="changePrimarySearchField"
          >
            <el-option
              v-for="field in fieldOptions"
              :key="field.field"
              :label="field.title"
              :value="field.field"
            />
          </el-select>
          <el-select
            class="width-100 gap-10"
            v-model="primarySearchType"
            size="small"
            value-key="id"
          >
            <el-option
              v-for="type in typeOptions"
              :key="type.value"
              :label="type.label"
              :value="type.value"
            />
          </el-select>

          <el-input
            v-if="!dropDownFlag && primarySearchPtype !== 'date'"
            id="input"
            v-model="primarySearchValue"
            class="width-150 gap-10 value-input search-input"
          >
            <el-button
              type="primary"
              class="search-button"
              slot="append"
              icon="advanced-query"
              @click="onSearch"
            ></el-button>
          </el-input>

          <div class="dropSelect" style="display: inline-block">
            <el-date-picker
              v-if="primarySearchPtype === 'date'"
              type="date"
              size="small"
              class="width-170"
              placeholder="请选择"
              v-model="primarySearchValue"
              value-format="yyyy-MM-dd HH:mm:ss"
            >
            </el-date-picker>
            <div
              v-if="primarySearchPtype === 'date'"
              class="append"
              @click="onSearch"
            ></div>
          </div>

          <div class="dropSelect" style="display: inline-block">
            <el-select
              v-if="dropDownFlag && primarySearchPtype !== 'date'"
              class="width-150 gap-10"
              v-model="primarySearchValue"
              size="small"
              value-key="id"
            >
              <el-option
                v-for="type in dropDownJson"
                :key="type.value"
                :label="type.name"
                :value="type.id"
              />
            </el-select>
            <div v-if="dropDownFlag" class="append" @click="onSearch"></div>
          </div>
        </div>
        <div>
          <slot name="head-operation"></slot>
        </div>
        <div>
          <el-button
            size="mini"
            class="custom-button"
            type="primary"
            @click="clickPrimarySearchBtn"
          >
            搜索
          </el-button>
          <el-button size="mini" type="" @click="resetSearch">重置</el-button>
        </div>
        <div class="query-history">
          <div>
            <span>查询历史</span>
          </div>
          <div class="tag-container" v-if="savedSearchRecords.length > 0">
            <el-tag
