              size="small"
              v-for="record in savedSearchRecords"
              class="hover-cursor"
              @close="handleSearchRecordTagClose(record.id)"
              @click="searchRecordTagClick(record.id)"
              closable
            >
              {{ record.name }}
            </el-tag>
          </div>
        </div>
      </div>
