                  <!-- 连接类型选项 -->
                  <el-select
                    class="width-80 margin-right-10 width-65"
                    placeholder="请选择"
                    size="mini"
                    v-model="condition5Group1.type"
                  >
                    <el-option
                      v-for="(type, index) in condition5Group1.typeOptions"
                      :key="index"
                      :label="type.label"
                      :value="type.value"
                    >
                    </el-option>
                  </el-select>
                  <!-- 输入值 -->
                  <el-input
                    v-if="
                      condition5Group1inputType === 'primary' &&
                      !condition5Group1.dropDownFlag
                    "
                    class="width-170"
                    size="mini"
                    v-model="condition5Group1.value"
                  >
                  </el-input>
                  <el-date-picker
                    v-if="
                      condition5Group1inputType === 'date' &&
                      !condition5Group1.dropDownFlag
                    "
                    type="date"
                    size="small"
                    class="width-170"
                    placeholder="请选择"
                    v-model="condition5Group1.value"
                    value-format="yyyy-MM-dd HH:mm:ss"
                  >
                  </el-date-picker>
                  <el-select
                    v-if="condition5Group1.dropDownFlag"
                    class="width-170 gap-10"
                    v-model="condition5Group1.value"
                    size="small"
                    value-key="id"
                  >
                    <el-option
                      v-for="type in condition5Group1.dropDownJson"
                      :key="type.value"
                      :label="type.name"
                      :value="type.id"
                    />
                  </el-select>
                </el-form-item>
                <el-form-item :class="[areaK, 'column-start-3']">
                  <!-- 逻辑运算选项 -->
                  <el-select
                    class="width-60 margin-right-10"
                    size="mini"
                    v-model="condition5Group2.operator"
                  >
                    <el-option
                      v-for="(operator, index) in operatorOptions"
                      :key="index"
                      :label="operator.label"
                      :value="operator.value"
                    >
                    </el-option>
                  </el-select>
                  <!-- 列名选项 -->
                  <el-select
                    class="width-120 margin-right-10"
                    placeholder="请选择"
                    size="mini"
                    v-model="condition5Group2.column"
                    @change="(v) => changeSearchField('condition5Group2', v)"
                  >
                    <el-option
                      v-for="(field, index) in fieldOptions"
                      :key="index"
                      :label="field.title"
                      :value="field.field"
                    >
                    </el-option>
                  </el-select>
                  <!-- 连接类型选项 -->
                  <el-select
                    class="width-80 margin-right-10 width-65"
                    placeholder="请选择"
                    size="mini"
                    v-model="condition5Group2.type"
                  >
                    <el-option
                      v-for="(type, index) in condition5Group2.typeOptions"
                      :key="index"
                      :label="type.label"
                      :value="type.value"
                    >
                    </el-option>
                  </el-select>
                  <!-- 输入值 -->
                  <el-input
                    v-if="
                      condition5Group2inputType === 'primary' &&
                      !condition5Group2.dropDownFlag
                    "
                    class="width-170"
                    size="mini"
                    v-model="condition5Group2.value"
                  >
                  </el-input>
                  <el-date-picker
                    v-if="
                      condition5Group2inputType === 'date' &&
                      !condition5Group2.dropDownFlag
                    "
                    type="date"
                    size="small"
                    class="width-170"
                    placeholder="请选择"
                    v-model="condition5Group2.value"
                    value-format="yyyy-MM-dd HH:mm:ss"
                  >
                  </el-date-picker>
                  <el-select
                    v-if="condition5Group2.dropDownFlag"
                    class="width-170 gap-10"
                    v-model="condition5Group2.value"
                    size="small"
                    value-key="id"
                  >
                    <el-option
                      v-for="type in condition5Group2.dropDownJson"
                      :key="type.value"
                      :label="type.name"
                      :value="type.id"
