        </div>
      </template>
      <template #footer>
        <el-button
          class="btn-padding margin-left-auto"
          @click="closeComplexSearchModal"
        >
          取消
        </el-button>
        <el-button class="btn-padding" @click="handleResetSearchConditionEvent">
          重置
        </el-button>
        <el-button class="btn-padding" @click="handleSaveHistoryEvent">
          保存
        </el-button>
        <el-button
          type="primary"
          class="btn-padding margin-right-auto"
          @click="handleSearchEvent"
        >
          查询
        </el-button>
      </template>
    </modal-vxe>

    <!-- 输入搜索记录名称的对话框 -->
    <modal-vxe
      ref=""
      :show="searchRecordDialog.isShow"
      :modalID="'saveSearchRecord' + ID"
      :modalName="searchRecordDialog.title"
      :width="450"
      :height="250"
      :marginSize="-100"
      @close="closeSaveSearchRecordModal"
    >
      <template #default>
        <div>
          <div class="flex-directon-row">
            <div class="flex-directon-row-title">搜索记录名称：</div>
            <el-input
              v-model="searchRecordName"
              size="small"
              class="flex-directon-row-input"
            />
          </div>
          <div class="flex-directon-row">
            <div class="flex-directon-row-title">保存为默认：</div>
            <div style="margin-right: 15px">
              <el-radio-group v-model="status">
                <el-radio v-model="status" :label="1">是</el-radio>
                <el-radio v-model="status" :label="0">否</el-radio>
              </el-radio-group>
            </div>
          </div>
        </div>
