      <template #footer>
        <el-button
          class="btn-padding margin-left-auto"
          @click="closeSaveSearchRecordModal"
        >
          取消
        </el-button>
        <el-button
          type="primary"
          class="btn-padding margin-right-auto"
          @click="searchRecordDialogSaveBtnClick"
        >
          保存
        </el-button>
      </template>
    </modal-vxe>
  </div>
</template>

<script>
import debounce from 'lodash/debounce';
import {
  showWarningMessage,
  showErrorMessage,
  showSuccessMessage
} from '@/utils/message.js';

import {
  // 根据id获取搜索记录的详情
  getSearchRecordDetailAPI,
  // 根据id删除某条搜索记录
  deleteSearchRecordAPI,

  // 点击搜索按钮时，提交搜索日志
  recordSearchLogAPI,
  createSearchRecordAPI,
  // getMenuIdForUrlAPI,
  getDropDownListForSearchAPI,
  getSearchRecordListAPI,
  getShowListAPI,
  getShowListByIdAPI,
  getDropDownListByAPI
} from '@/api/searchBox';

import throttle from 'lodash/throttle';
import { getUrlParams, getUrlKey } from '@/utils/common';
import { getPtype } from '@/utils/dateFormat';
import { isNonemptyArray } from '@/utils/array';
// import { getUrlKey } from '@/utils/common';
import bus from '@/plugins/bus';

export default {
  name: 'headerSearch',
  props: {
    // cssType
    cssType: {
      type: [String, Number],
      default: 1
    },
    // 判断搜索按钮是否显示
    showPrimarySearch: {
      type: Boolean,
      default: false
    },
    disabledSearch: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    $route(to, from) {
      // console.log('to:', to);
      // console.log('from:', from);
      // if (from.path === '/setCommonFunctions') {
      //   }
      this.getShowList(this.pageId);
    }
  },
  data() {
    return {
      primarySearchPtype: '',
      complexSearchModalName: '复杂查询',
      status: 0,
      // 输入搜索记录名称的对话框有关
      searchRecordDialog: {
        title: '输入搜索记录名称',
        isShow: false
      },

      // 搜索记录名称的输入
      searchRecordName: '',

      // 当前页面已保存的搜索记录
      savedSearchRecords: [],
      // 当前页面保存的input
      searchInputs: [],

      // 当前要保存的搜索记录
      currentUsingSearchRecord: {
        conditionJson1: [],
        conditionJson2: [],
        operatorGroup: 1
      },

      // 保存搜索内输入的搜索条件，防止翻页时变回无条件查询
      savedSearchCondition: [],
      // head
      head: {
        itemNumber: '',
        itemName: '',
        itemModel: '',
        timeRange: []
      },
      // 是否显示高级搜索
      isSearchBoxShowForTypeOne: false,
      title: '',

      primarySearchField: '', // 搜索表头字段

      fieldOptions: [], // 表头字段下拉选项

      primarySearchType: '', // 搜索类型

      primarSearchPtype: '', // 搜索字段类型

      typeOptions3: [], // 搜索类型下拉选项

      typeOptions2: [], // 获取到的原始类型下拉选项

      typeOptions: [], // 复杂查询的连接符

      dropDownFlag: false, // false则显示输入框  true则显示下拉
      dropDownJson: [], // 简单搜索下拉0

      primarySearchValue: '', // 搜索值输入框

      // ------------------------高级搜索--------------------
      // 展开按钮图标
      expandBtnIcon: 'search-bar-expand',

      // 逻辑运算选项
      operatorOptions: [
        {
          label: '和',
          value: 1
        },
        {
          label: '或',
          value: 2
        }
      ],

      // 大组检逻辑运算下拉框的动态样式
      width80: 'width-80',
      rowStartThree: 'row-start-3',
      rowStartOne: 'row-start-1',
      columnExpand: 'column-expand',
      columnHide: 'column-hide',
      marginTop10: 'margin-top-10',
      formArea: 'form-area',
      areaA: 'area-a',
      areaB: 'area-b',
      areaC: 'area-c',
      areaD: 'area-d',
      areaE: 'area-e',
      areaF: 'area-f',
      areaG: 'area-g',
      areaH: 'area-h',
      areaI: 'area-i',
      areaJ: 'area-j',
      areaK: 'area-k',

      // 保存输入的搜索条件
      condition1Group1: {
        column: '',
        type: '',
        value: '',
        operator: 1
      },
      condition2Group1: {
        column: '',
        type: '',
        value: '',
        operator: 1
      },
      condition3Group1: {
        column: '',
        type: '',
        value: '',
        operator: 1
      },
      condition4Group1: {
        column: '',
        type: '',
        value: '',
        operator: 1
      },
      condition5Group1: {
        column: '',
        type: '',
        value: '',
        operator: 1
      },
      condition1Group2: {
        column: '',
        type: '',
        value: '',
        operator: 1
      },
      condition2Group2: {
        column: '',
        type: '',
        value: '',
        operator: 1
      },
      condition3Group2: {
        column: '',
        type: '',
        value: '',
        operator: 1
      },
      condition4Group2: {
        column: '',
        type: '',
        value: '',
        operator: 1
      },
      condition5Group2: {
        column: '',
        type: '',
        value: '',
        operator: 1
      },
      // 大组之间的逻辑运算
      groupOperator: 1,
      // 是否显示隐藏的条件输入行
      isHiddenSearchLineShow: false,
      // 记录模态框状态
      modalState: [],
      ID: '',
      modalName: 'complexSearch' + this.ID
    };
  },
  created() {
    const { id } = getUrlParams();
    this.ID = id;
    // console.log(1111111, document.getElementById('headerSearch').offsetHeight);
  },
  mounted() {
    this.initPrimarySearchOptions();
    const { id } = getUrlParams();
    this.pageId = id;
    this.getSearchRecords(this.pageId);
    this.getShowList(this.pageId);
    // 页面开始监听回车事件
    document.addEventListener('keyup', this.enterKeyUp);
  },
  beforeDestroy() {
    // 移除回车事件监听
    document.removeEventListener('keyup', this.enterKeyUp);
  },
  computed: {
    condition1Group1inputType() {
      if (
        /Time$/.test(this.condition1Group1.column) ||
        /Date$/.test(this.condition1Group1.column)
      ) {
        return this.filterType(this.condition1Group1.column);
      }
      return 'primary';
    },
    condition2Group1inputType() {
      if (
        /Time$/.test(this.condition2Group1.column) ||
        /Date$/.test(this.condition2Group1.column)
      ) {
        return this.filterType(this.condition2Group1.column);
      }

      return 'primary';
    },
    condition3Group1inputType() {
      if (
        /Time$/.test(this.condition3Group1.column) ||
        /Date$/.test(this.condition3Group1.column)
      ) {
        return this.filterType(this.condition3Group1.column);
      }

      return 'primary';
    },
    condition4Group1inputType() {
      if (
        /Time$/.test(this.condition4Group1.column) ||
        /Date$/.test(this.condition4Group1.column)
      ) {
        return this.filterType(this.condition4Group1.column);
      }

      return 'primary';
    },
    condition5Group1inputType() {
      if (
        /Time$/.test(this.condition5Group1.column) ||
        /Date$/.test(this.condition5Group1.column)
      ) {
        return this.filterType(this.condition5Group1.column);
      }

      return 'primary';
    },
    condition1Group2inputType() {
      if (
        /Time$/.test(this.condition1Group2.column) ||
        /Date$/.test(this.condition1Group2.column)
      ) {
        return this.filterType(this.condition1Group2.column);
      }

      return 'primary';
    },
    condition2Group2inputType() {
      if (
        /Time$/.test(this.condition2Group2.column) ||
        /Date$/.test(this.condition2Group2.column)
      ) {
        return this.filterType(this.condition2Group2.column);
      }

      return 'primary';
    },
    condition3Group2inputType() {
      if (
        /Time$/.test(this.condition3Group2.column) ||
        /Date$/.test(this.condition3Group2.column)
      ) {
        return this.filterType(this.condition3Group2.column);
      }

      return 'primary';
    },
    condition4Group2inputType() {
      if (
        /Time$/.test(this.condition4Group2.column) ||
        /Date$/.test(this.condition4Group2.column)
      ) {
        return this.filterType(this.condition4Group2.column);
      }

      return 'primary';
    },
    condition5Group2inputType() {
      if (
        /Time$/.test(this.condition5Group2.column) ||
        /Date$/.test(this.condition5Group2.column)
      ) {
        return this.filterType(this.condition5Group2.column);
      }

      return 'primary';
    }
  },
  methods: {
    // 过滤日期个别字段
    filterType(param) {
      var arr = ['dayWorkTime'];
      if (arr.indexOf(param) > -1) {
        return 'primary';
      } else {
        return 'date';
      }
    },
    /**
     * @description: 如果按下的是回车键，触发“搜索”按钮点击事件
     * @param {object} event 事件对象
     * @return void
     * @author: zhuhongfei
     */
    enterKeyUp(event) {
      if (
        event.key === 'Enter' &&
        event.target.id &&
        event.target.id.indexOf('input') > -1
      ) {
        this.clickPrimarySearchBtn();
      }
    },

    /**
     * @description: （搜索框）点击“保存”按钮，触发回调
     * @param 无
     * @return: void
     * @author: zhf
     */
    handleSaveSearchRecordEvent(...conditionArr) {
      // 如果searchBox组件只传了一个参数
      if (conditionArr.length === 1) {
        this.currentUsingSearchRecord.conditionJson1.splice(0);
        this.currentUsingSearchRecord.conditionJson1.push(...conditionArr[0]);
      }

      // 如果searchBox组件传了3个参数
      if (conditionArr.length === 3) {
        // 保存第一组条件
        this.currentUsingSearchRecord.conditionJson1.splice(0);
        this.currentUsingSearchRecord.conditionJson1.push(...conditionArr[0]);
        // 保存第二组条件
        this.currentUsingSearchRecord.conditionJson2.splice(0);
        this.currentUsingSearchRecord.conditionJson2.push(...conditionArr[2]);
        // 保存大组间的连接符
        this.currentUsingSearchRecord.operatorGroup = conditionArr[1];
      }

      // 如果当前显示了某条已经存在的记录，也不能保存
      this.searchRecordDialog.isShow = !this.searchRecordDialog.isShow;
    },

    /**
     * @description: (搜索框)“重置”按钮点击触发事件
     * @param 无
     * @return: void
     * @author: zhf
     */
    handleResetSearchConditionEvent() {
      // 清空查询条件
      this.savedSearchCondition.splice(0);
      this.resetCondtionGroup();
      this.$emit('refreshTableData', []);
    },

    /**
     * @description:清空输入的数据
     * @param {*}
     * @return {*}
     * @author: ZHF
     */
    resetSearch() {
      this.primarySearchValue = '';
      this.searchInputs = this.searchInputs.map(row => ({ ...row, value: '' }));
      this.clickPrimarySearchBtn();
    },
    /**
     * @description: 点击“搜索”按钮，向父页面传搜索参数，提交自定义事件
     * @param {无}
     * @return {void}
     * @author: zhf
     */
    clickPrimarySearchBtn: debounce(function() {
      if (this.savedSearchCondition.length > 0) {
        this.savedSearchCondition.splice(0);
      }
      if (
        typeof this.primarySearchValue === 'string' &&
        this.primarySearchValue.trim() === ''
      ) {
        if (isNonemptyArray(this.searchInputs)) {
          var otherConditions1 = this.searchInputs
            .filter(item => item.value)
            .map(row => {
              return {
                column: row.property,
                type: row.type,
                ptype: row.ptype,
                value: row.value,
                operator: 1
              };
            });
        }
        this.$emit('refreshTableData', [otherConditions1]);
      } else {
        this.savedSearchCondition.push([
          {
            column: this.primarySearchField,
            type: this.primarySearchType,
            ptype: getPtype(this.primarySearchField),
            value: this.primarySearchValue,
            operator: 1
          }
        ]);
        var otherConditions2 = [];
        if (isNonemptyArray(this.searchInputs)) {
          otherConditions2 = this.savedSearchCondition[0];
          otherConditions2.push(
            ...this.searchInputs
              .filter(item => item.value)
              .map(row => {
                return {
                  column: row.property,
                  type: row.type,
                  ptype: row.ptype,
                  value: row.value,
                  operator: 1
                };
              })
          );
        } else {
          otherConditions2 = this.savedSearchCondition[0];
        }
        this.$emit('refreshTableData', [otherConditions2]);
      }
    }, 300),
    // ----------------------------------简单搜索--------------------------------

    /**
     * @description: 当searchBox组件中的某条记录被成功删除时，主页面需要刷新搜索记录列表
     * @param {number} cssType 区分同一路由下的页面，
     *                 1代表主页，2在生产任务模块中代表样式二页面，在其它模块中代表操作记录页面
     * @return void
     * @author: cyb
     */
    handleRecordDeleteEvent(cssType = this.cssType) {
      this.getSearchRecords(this.pageId, cssType);
      this.getShowList(this.pageId);
    },
    /**
     * @description: 初始化简单搜索的下拉选项
     * @param {无}
     * @return {void}
     * @author: zhf
     */
    async initPrimarySearchOptions(cssType = this.cssType) {
      try {
        // const query = {
        //   url: this.$route.path
        // };
        // const { id } = await getMenuIdForUrlAPI(query);

        const head = {
          menuId: getUrlKey('id'),
          cssType
        };
        const {
          propertyDropDown,
          characterDropDown,
          characterDropDown2
        } = await getDropDownListForSearchAPI(head);
        const formatteredPropertyDropDown = propertyDropDown.map(
          ({ label, value, dropDownFlag, ptype, dropDownJson, dropDownSql, dropDownUrl }) => ({
            title: label,
            field: value,
            ptype,
            dropDownFlag,
            dropDownJson,
            dropDownSql,
            dropDownUrl

          })
        );
        // 为表头选项下拉框赋值
        if (
          Array.isArray(formatteredPropertyDropDown) &&
          formatteredPropertyDropDown.length > 0
        ) {
          this.fieldOptions.splice(0);
          this.fieldOptions.push(...formatteredPropertyDropDown);
          this.primarySearchField = this.fieldOptions[0] ? this.fieldOptions[0].field : '';
        }
        this.dropDownFlag = this.fieldOptions[0].dropDownFlag;

        if (this.dropDownFlag) {
          // const res = await getDropDownListByAPI();
          this.dropDownJson = this.fieldOptions[0].dropDownJson;
        }

        console.log(111, this.fieldOptions[0], characterDropDown2[this.fieldOptions[0].ptype]);
        this.typeOptions = this.fieldOptions[0] ? characterDropDown2[this.fieldOptions[0].ptype] : [];
        this.typeOptions2 = Object.assign({}, characterDropDown2);
        this.typeOptions3 = { ...characterDropDown };
        console.log(characterDropDown2[this.fieldOptions[0].ptype][0]);
        this.primarySearchType = characterDropDown2[this.fieldOptions[0].ptype][0].value;
        this.primarySearchPtype = this.fieldOptions[0].ptype;
        // console.log('111', this.fieldOptions);
      } catch (error) {
        // console.log('222', this.fieldOptions);
        // showErrorMessage(error);
      }
    },
    /**
     * @description: 复杂搜索按钮点击，触发回调
     * @param 无
     * @return void
     * @author: YZZ
     */
    onSearch() {
      if (this.disabledSearch) {
        return;
      }
      this.showSearchCard();
    },
    /**
     * @description: （主页面）点击“搜索”按钮，打开搜索框
     * @param 无
     * @return: void
     * @author: zhf
     */
    async showSearchCard() {
      this.isSearchBoxShowForTypeOne = !this.isSearchBoxShowForTypeOne;
      try {
        const { id } = getUrlParams();
        this.pageId = id;

        // this.getFieldAndOperatorDropDownList(this.pageId);
      } catch (error) {
        // showErrorMessage(error);
      }
    },

    /**
     * @description: 字段搜索框值改变后
     * @param {无}
     * @return {void}
     * @author: zhf
     */
    async changePrimarySearchField(e) {
      const row = this.fieldOptions.filter(item => item.field === e)[0] || {};
      console.log(row);
      this.typeOptions = [];
      this.typeOptions.push(...this.typeOptions2[row.ptype]);
      this.primarySearchValue = '';
      this.primarySearchType = this.typeOptions[0] ? this.typeOptions[0].value : '';
      this.primarySearchPtype = row.ptype;
      if (row.dropDownFlag) {
        this.dropDownFlag = row.dropDownFlag;
        if (row.dropDownJson) {
          // this.dropDownJson = JSON.parse(row.dropDownJson);
          this.dropDownJson = row.dropDownJson;
        } else {
          const res = await getDropDownListByAPI(row.dropDownUrl);
          this.dropDownJson = res;
        }
      } else {
        this.dropDownFlag = false;
      }
    },

    /**
     * @description: 复杂查询字段搜索框值改变后
     * @param {无}
     * @return {void}
     * @author: zhf
     */
    async changeSearchField(type, e) {
      const row = this.fieldOptions.filter(item => item.field === e)[0];
      this[type].typeOptions = row ? this.typeOptions2[row.ptype] : [];
      this[type].type = this[type].typeOptions[0] ? this[type].typeOptions[0].value : '';
      this[type].value = '';
      if (row.dropDownFlag) {
        if (row.dropDownJson) {
          // this[type].dropDownJson = JSON.parse(row.dropDownJson);
          this[type].dropDownJson = row.dropDownJson;
          this[type].dropDownFlag = true;
        } else {
          const res = await getDropDownListByAPI(row.dropDownUrl);
          this[type].dropDownJson = res;
          this[type].dropDownFlag = true;
        }
      } else {
        this[type].dropDownFlag = false;
      }
      this[type] = { ...this[type] };
    },
    /**
     * @description: 根据menuId和cssType获取表头和查询类型的选项下拉框
     * @param {number} menuId 路由的id
     * @param {number} cssType 同一路由下的不同页面标志
     * @return promise
     * @author: zhf
     */
    async getFieldAndOperatorDropDownList(menuId, cssType = this.cssType) {
      try {
        const query = {
          menuId,
          cssType
        };
        const {
          propertyDropDown,
          characterDropDown,
          characterDropDown2
        } = await getDropDownListForSearchAPI(query);
        const formatteredPropertyDropDown = propertyDropDown.map(
          ({ label, value, dropDownFlag, ptype, dropDownJson, dropDownSql, dropDownUrl }) => ({
            title: label,
            field: value,
            ptype,
            dropDownFlag,
            dropDownJson,
            dropDownSql,
            dropDownUrl
          })
        );
        this.fieldOptions.splice(0);
        // 为表头选项下拉框赋值
        this.fieldOptions.push(...formatteredPropertyDropDown);

        // 搜索框初始化之后，为字段选项赋初始值
        if (this.fieldOptions.length > 0 && this.fieldOptions[0].field) {
          for (let i = 1; i < 3; i++) {
            for (let j = 1; j < 6; j++) {
              this[
                `condition${j}Group${i}`
              ].column = this.fieldOptions[0].field;
            }
          }
        }

        this.typeOptions.splice(0);
        this.typeOptions = this.fieldOptions[0] ? characterDropDown2[this.fieldOptions[0].ptype] : [];
        this.typeOptions2 = Object.assign({}, characterDropDown2);
        this.typeOptions3 = Object.assign({}, characterDropDown);
        if (this.fieldOptions[0] && this.fieldOptions[0].dropDownFlag) {
          this.dropDownFlag = this.fieldOptions[0].dropDownFlag;
          if (this.fieldOptions[0].dropDownJson) {
            // this.dropDownJson = JSON.parse(this.fieldOptions[0].dropDownJson);
            this.dropDownJson = this.fieldOptions[0].dropDownJson;
          } else {
            const res = await getDropDownListByAPI(this.fieldOptions[0].dropDownUrl);
            this.dropDownJson = res;
          }
        }
        // 搜索框初始化之后，为查询类型选项赋初始值
        if (this.typeOptions.length > 0) {
          for (let i = 1; i < 3; i++) {
            for (let j = 1; j < 6; j++) {
              this[`condition${j}Group${i}`].typeOptions = this.fieldOptions[0] ? characterDropDown2[this.fieldOptions[0].ptype] : [];
              this[`condition${j}Group${i}`].dropDownJson = [...this.dropDownJson];
              this[`condition${j}Group${i}`].type = this.typeOptions[0].value;
              this[`condition${j}Group${i}`].dropDownFlag = this.fieldOptions[0].dropDownFlag;
            }
          }
        }
        // this.clearComplexSearchModalValue();
      } catch (error) {
        // showErrorMessage(error || '出现未知错误！');
      }
    },
    /**
     * @description: 复杂查询模态框关闭
     * @param {}
     * @return:
     * @author: YZZ
     */
    closeComplexSearchModal() {
      // this.clearComplexSearchModalValue();
      this.isSearchBoxShowForTypeOne = false;
    },
    /**
     * @description: 复杂查询模态框输入框清空方式
     * @param {}
     * @return:
     * @author: YZZ
     */
    clearComplexSearchModalValue() {
      for (let i = 1; i < 3; i++) {
        for (let j = 1; j < 6; j++) {
          // 清空 查询框数据
          this[`condition${j}Group${i}`].value = '';
        }
      }
    },
    /**
     * @description:获取获取默认的搜索
     * @param {*}
     * @return {*}
     * @author: ZHF
     */
    async getShowList(menuId, cssType = this.cssType) {
      try {
        const query = {
          menuId: menuId,
          cssType
        };

        if (isNonemptyArray(this.searchInputs)) {
          return;
        }
        const res = await getShowListAPI(query);
        if (isNonemptyArray(res)) {
          const newData = res.map(item => {
            return { ...item, value: '' };
          });
          this.searchInputs.splice(0);
          this.searchInputs.push(...newData);
          bus.$emit('val', 1);
        } else {
          bus.$emit('val', 0);
        }
      } catch (error) {
        // showErrorMessage(error);
      }
    },
    /**
     * @description:获取对应搜索记录的搜索
     * @param {*}
     * @return {*}
     * @author: ZHF
     */
    async getShowListById(id) {
      try {
        const query = {
          id
        };
        const res = await getShowListByIdAPI(query);
        if (isNonemptyArray(res)) {
          //
          const newData = res.map(item => {
            return { ...item, value: '' };
          });
          this.searchInputs.splice(0);
          this.searchInputs.push(...newData);
        }
      } catch (error) {
        // showErrorMessage(error);
      }
    },
    /**
     * @description: 获取当前页面存在的搜索记录
     * @param {number} menuId 当前路由的id
     * @return promise
     * @author: zhf
     */
    async getSearchRecords(menuId, cssType = this.cssType) {
      try {
        // const account = window.localStorage.getItem('account');
        const query = {
          // account,
          menuId: menuId,
          cssType
        };
        const res = await getSearchRecordListAPI(query);
        this.savedSearchRecords.splice(0);
        this.savedSearchRecords.push(...res);
        // console.log(this.savedSearchRecords);
      } catch (error) {
        // showErrorMessage(error);
      }
    },
    /**
     * @description: （输入搜索记录名称对话框）点击“保存”按钮，触发回调
     * @param 无
     * @return void
     * @author: zhf
     */
    searchRecordDialogSaveBtnClick() {
      // 如果未输入记录名称，提示先输入名称再保存
      if (this.searchRecordName === '') {
        showWarningMessage('保存之前请输入记录名称！');
        return false;
      }

      const hasRepeatName = this.savedSearchRecords.some(record => {
        return record.name === this.searchRecordName;
      });

      // 输入的名称与已存在的搜索记录存在重名，提示重新输入
      if (hasRepeatName) {
        showErrorMessage('搜索记录名称已存在，请重新输入！');
        return false;
      }

      // 保存记录
      this.saveSearchRecord();
    },
    /**
     * @description: 调用保存搜索记录的接口，保存本条记录
     * @param 无
     * @return void
     * @author: zhf
     */
    async saveSearchRecord(cssType = this.cssType) {
      try {
        // 获取用户名
        // const account = window.localStorage.getItem('account');
        const query = {
          // account,
          menuId: this.pageId,
          name: this.searchRecordName,
          cssType,
          status: this.status
        };

        const res = await createSearchRecordAPI(
          query,
          this.currentUsingSearchRecord
        );

        if (res.code === '0000') {
          showSuccessMessage(res.message);
          // 刷新右侧记录列表
          this.getSearchRecords(this.pageId);
          this.getShowList(this.pageId);
          this.isSearchBoxShowForTypeOne = false;
        } else {
          showErrorMessage(res.message || '保存失败！');
        }
      } catch (error) {
        // showErrorMessage(error || '出现未知错误！');
      } finally {
        this.closeSaveSearchRecordModal();
      }
    },
    /**
     * @description: （输入搜索记录名称对话框）点击“取消”按钮，触发回调
     * @param 无
     * @return void
     * @author: zhf
     */
    closeSaveSearchRecordModal() {
      console.log('执行取消');
      // 清空搜索记录输入框的输入
      this.searchRecordName = '';
      this.status = 0;
      this.currentUsingSearchRecord = Object.assign(
        {},
        {
          conditionJson1: [],
          conditionJson2: [],
          operatorGroup: 1
        }
      );
      this.searchRecordDialog = Object.assign(
        {},
        {
          title: '输入搜索记录名称',
          isShow: false
        }
      );
    },

    // ------------------------高级搜索-----------------
    /**
     * @description: （搜索框）判断某组条件是否填写完整
     * @param {object} condition
     * @return: boolean
     * @author: zhf
     */
    checkCondition(condition) {
      for (const key of Object.keys(condition)) {
        if (!condition[key]) {
          return false;
        }
      }

      return true;
    },

    /**
     * @description: （搜索框）点击“查询”按钮，触发方法
     * @param 无
     * @return: void
     * @author: zhf
     */
    handleSearchEvent() {
      console.log('执行复杂查询');
      let pageComponent = this.$parent;
      pageComponent = pageComponent.$parent;

      console.log(pageComponent);

      const conditionArr1 = [];
      const conditionArr2 = [];
      for (let index = 1; index < 6; index++) {
        const element = { ...this[`condition${index}Group1`] };
        delete element.dropDownFlag;
        delete element.typeOptions;
        delete element.dropDownJson;
        element.ptype = this.returnPtype(element.column);
        // 将填写完整的条件加入数组
        if (this.checkCondition(element)) {
          conditionArr1.push(element);
        }
      }

      for (let index = 1; index < 6; index++) {
        const element = { ...this[`condition${index}Group2`] };
        delete element.dropDownFlag;
        delete element.typeOptions;
        delete element.dropDownJson;
        element.ptype = this.returnPtype(element.column);
        // 将填写完整的条件加入数组
        if (this.checkCondition(element)) {
          conditionArr2.push(element);
        }
      }

      // 如果没有一组条件填写完整，则不做查询
      if (conditionArr1.length === 0 && conditionArr2.length === 0) {
        showWarningMessage('查询条件未填写完整！');
        return false;
      }

      console.log(pageComponent);

      const { cssType = this.cssType, pageId } = pageComponent;
      const searchCondition = {
        conditionJson1: conditionArr1,
        operatorGroup: this.groupOperator,
        conditionJson2: conditionArr2,
        current: 1,
        size: 10
      };
      this.submitSearchRecord(cssType, pageId, searchCondition);

      // 如果只填写了第一组的条件，将第一组的条件传给父页面进行查询
      if (conditionArr1.length > 0 && conditionArr2.length === 0) {
        // 提交搜索条件
        this.$emit('refreshTableData', [conditionArr1]);
      }

      // 如果只填写了第二组的条件，将第二组的条件传给父页面进行查询
      if (conditionArr2.length > 0 && conditionArr1.length === 0) {
        // 提交搜索条件
        this.$emit('refreshTableData', [conditionArr2]);
      }

      // 如果两组条件都有填写，则将两个数组以及数组间的逻辑运算类型都作为参数传给父页面
      if (conditionArr1.length > 0 && conditionArr2.length > 0) {
        // 根据搜索条件查询数据
        this.$emit('refreshTableData', [
          conditionArr1,
          this.groupOperator,
          conditionArr2
        ]);
      }

      // 隐藏复杂查询框
      this.isSearchBoxShowForTypeOne = false;
    },

    /**
     * @description: 点击搜索按钮时，向后端提交搜索记录
     * @param {number} cssType
     * @param {number} menuId
     * @param {object} searchCondition
     * @author: zhf
     */
    submitSearchRecord: throttle(
      async function(cssType, menuId, searchCondition) {
        try {
          const res = await recordSearchLogAPI(
            { cssType, menuId },
            searchCondition
          );
          if (res.code !== '0000') {
            // showErrorMessage(res.message);
          }
        } catch (error) {
          // showErrorMessage(error);
        }
      },
      1000,
      { leading: true }
    ),

    /**
     * @description: “保存”按钮点击事件
     * @param 无
     * @return void
     * @author: zhf
     */
    handleSaveHistoryEvent() {
      const conditionArr1 = [];
      const conditionArr2 = [];

      // this[`condition${j}Group${i}`].typeOptions = this.fieldOptions[0] ? characterDropDown2[this.fieldOptions[0].ptype] : [];
      // this[`condition${j}Group${i}`].dropDownJson = [...this.dropDownJson];
      // this[`condition${j}Group${i}`].type = this.typeOptions[0].value;
      // this[`condition${j}Group${i}`].dropDownFlag = this.fieldOptions[0].dropDownFlag;
      for (let index = 1; index < 6; index++) {
        const element = this[`condition${index}Group1`];
        delete element.dropDownFlag;
        delete element.typeOptions;
        delete element.dropDownJson;
        element.ptype = this.returnPtype(element.column);
        // 将填写完整的条件加入数组
        if (this.checkCondition(element)) {
          conditionArr1.push(element);
        }
      }

      for (let index = 1; index < 6; index++) {
        const element = this[`condition${index}Group2`];
        delete element.dropDownFlag;
        delete element.typeOptions;
        delete element.dropDownJson;
        element.ptype = this.returnPtype(element.column);
        // 将填写完整的条件加入数组
        if (this.checkCondition(element)) {
          conditionArr2.push(element);
        }
      }

      // 如果没有一组条件填写完整，则不做保存
      if (conditionArr1.length === 0 && conditionArr2.length === 0) {
        showWarningMessage('查询条件未填写完整！');
        return false;
      }

      // 如果只填写了第一组的条件，将第一组的条件传给父页面进行保存
      if (conditionArr1.length > 0 && conditionArr2.length === 0) {
        // 提交搜索条件
        this.handleSaveSearchRecordEvent(conditionArr1);
      }

      // 如果只填写了第二组的条件，将第二组的条件传给父页面进行保存
      if (conditionArr2.length > 0 && conditionArr1.length === 0) {
        // 提交搜索条件
        this.handleSaveSearchRecordEvent(conditionArr2);
      }

      // 如果两组条件都有填写，则将两个数组以及数组间的逻辑运算类型都作为参数传给父页面
      if (conditionArr1.length > 0 && conditionArr2.length > 0) {
        // 根据搜索条件查询数据
        this.handleSaveSearchRecordEvent(
          conditionArr1,
          this.groupOperator,
          conditionArr2
        );
      }
    },

    /**
     * @description: （搜索框）清除所有条件输入
     * @param 无
     * @return: void
     * @author: zhf
     */
    resetCondtionGroup() {
      console.log(666);
      for (let i = 1; i < 3; i++) {
        for (let j = 1; j < 6; j++) {
          this[`condition${j}Group${i}`] = Object.assign(
            {},
            this[`condition${j}Group${i}`],
            {
              column: '',
              type: '',
              value: '',
              operator: 1
            }
          );
        }
      }

      this.groupOperator = 1;
    },

    /**
     * @description: （搜索框）点击关闭某条搜索记录
     * @param {number} id 搜索记录的id
     * @return promise
     * @author: zhf
     */
    handleSearchRecordTagClose(id) {
      this.$confirm('此操作将删除该条搜索记录, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          deleteSearchRecordAPI({ id: id })
            .then(res => {
              if (res.code === '0000') {
                this.handleRecordDeleteEvent();
              } else {
                showErrorMessage(res.message || '操作失败！');
              }
            })
            .catch(err => {
              console.log(err);
              // showErrorMessage(err);
            });
        })
        .catch(() => {
          // this.$message({
          //   type: 'info',
          //   message: '已取消删除'
          // });
        });
    },

    /**
     * @description: （搜索框）点击某条搜索记录tag，触发回调
     * @param {number} id 搜索记录的id
     * @return promise
     * @author: zhf
     */
    async searchRecordTagClick(id) {
      this.getShowListById(id);
      try {
        const res = await getSearchRecordDetailAPI({ id });
        const { group1, group2, operatorGroup } = res;
        this.isHiddenSearchLineShow = true;
        if (Array.isArray(group1) && Array.isArray(group2)) {
          // 先清空前一组的条件
          this.resetCondtionGroup();
          if (group1.length > 0 && group2.length === 0) {
            // console.log(group1);
            this.fillInput(group1, 1);
            const conditionArr = group1.map(
              ({ operator, property, ptype, type, value }) => ({
                column: property,
                operator,
                ptype,
                type,
                value
              })
            );
            // 让父页面刷新表格
            this.$emit('refreshTableData', [conditionArr]);
          } else if (group2.length > 0 && group1.length === 0) {
            // console.log(group2);
            this.fillInput(group2, 1);
            const conditionArr = group2.map(
              ({ operator, property, ptype, type, value }) => ({
                column: property,
                operator,
                ptype,
                type,
                value
              })
            );
            // 让父页面刷新表格
            this.$emit('refreshTableData', [conditionArr]);
          } else if (group2.length > 0 && group1.length > 0) {
            this.fillInput(group1, 1);
            this.fillInput(group2, 2);
            this.groupOperator = operatorGroup;
            const conditionArr1 = group1.map(
              ({ operator, property, ptype, type, value }) => ({
                column: property,
                operator,
                ptype,
                type,
                value
              })
            );
            const conditionArr2 = group2.map(
              ({ operator, property, ptype, type, value }) => ({
                column: property,
                operator,
                ptype,
                type,
                value
              })
            );

            // 让父页面刷新表格
            this.$emit('refreshTableData', [
              conditionArr1,
              this.groupOperator,
              conditionArr2
            ]);
          }
        }
      } catch (error) {
        // showErrorMessage(error);
      }
    },

    /**
     * @description: 将搜索条件存入搜索框
     * @param {array} arr 某一小组的搜索条件
     * @param {number} group 指定哪一个小组
     * @return void
     * @author: zhf
     */
    fillInput(arr, group) {
      for (let i = 0; i < arr.length; i++) {
        const { property, type, value, operator } = arr[i];
        this[`condition${i + 1}Group${group}`] = Object.assign(
          {},
          this[`condition${i + 1}Group${group}`],
          {
            column: property,
            type,
            value,
            operator
          }
        );
      }
    },

    /**
     * @description: 根据选择的字段是否与时间有关，返回对应的ptype
     * @param {string} columnName field名称
     * @return {string}
     * @author: zhf
     */
    returnPtype(columnName) {
      const isTimeColumn =
        /(Time)$/.test(columnName) || /(Date)$/.test(columnName);
      return isTimeColumn ? 'date' : 'string';
    }
  },
  components: {
    // complexQury
    // HeadOperationItem
  }
};
</script>

<style lang="less" scoped>
.el-input-group__append,
.el-input-group__prepend {
  background-color: #f5f7fa;
  color: #909399;
  vertical-align: middle;
  display: table-cell;
  position: relative;
  border: 1px solid #dcdfe6;
  border-radius: 4px;
  padding: 0 5px !important;
  width: 1px;
  white-space: nowrap;
}
.header {
  background-color: #ffffff;
  min-height: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-left-border {
  display: flex;
