import { Message } from 'element-ui';

/**
 * @description: 提示成功消息
 * @param {string} msg 消息内容
 * @return: void
 */
export function showSuccessMessage(msg) {
  Message.success({
    message: msg,
    duration: 1500,
    offset: 45,
    center: true,
    showClose: true
  });
}

/**
 * @description: 提示消息信息
 * @param {string} msg 消息内容
 * @return: void
 */
export function showInfoMessage(msg) {
  Message.info({
    message: msg,
    duration: 1500,
    offset: 45,
    center: true,
    showClose: true
  });
}

/**
 * @description: 提示警告信息
 * @param {string} msg 消息内容
 * @return: void
 */
export function showWarningMessage(msg) {
  if (window.localStorage.getItem('token') !== null) {
    Message.warning({
      message: msg,
      duration: 1500,
      offset: 45,
      center: true,
      showClose: true
    });
  }
}

/**
 * @description: 提示错误信息
 * @param {string} msg 消息内容
 * @return: void
 */
export function showErrorMessage(msg) {
  Message.error({
    message: msg,
    duration: 1500,
    offset: 45,
    center: true,
    showClose: true
  });
}
